import { get, merge } from "lodash-es";
import useComponentStore from "../components/store";
import { ActionType } from "../constant";
import { deepMap, makeReadOnly } from "../misc";
import Sandbox from "../sandbox/function";
import { useStatesStore } from "../states";
import { getComponentMethods } from "./methods";
import { enqueueSnackbar } from 'notistack';
import { compileExpression } from "../expression";
import { set } from "lodash-es";

const methodApi = {
  setStates: (id, states, merge = true) => {
    useStatesStore.getState().setState(id, states, merge);
  }
};

export const eventDeps = {};
const excludeResolveOptions = [
  'sourceCode'
];
// 
const resolveOptions = event => {
  const options = get(event, 'action.options');
  const expressionRegExp = /\{\{([\s\S]*?)\}\}/;
  const processedOptions = {};
  deepMap(options, (value, key, path) => {
    if (excludeResolveOptions.includes(path.join('.'))) {
      return;
    }
    if (typeof value !== 'string' || !expressionRegExp.test(value)) {
      return;
    }
    const [returnValue, fieldDeps] = compileExpression(value);
    set(processedOptions, path, returnValue);
    eventDeps[[event.id, event.eventId, ...path].join('.')] = fieldDeps;
  });
  return merge({}, options, processedOptions);
}

const processControlComponentAction = ({ id, eventId, type, action, processedOptions: options }) => {
  if (!options.target || !options.method) {
    return {
      ...action,
    }
  }
  const method = getComponentMethods(options.target)[options.method]?.callback;
  const context = {
    target: options.target,
    source: id,
    self: options.target,
    eventId,
    eventType: type,
    options,
    api: makeReadOnly(methodApi),
  };
  const callback = () => {
    return method(context);
  }
  return {
    ...action,
    callback,
  }
}

const processRunScriptAction = ({ id, eventId, type, action, processedOptions: options }) => {
  const { sourceCode } = options;
  const callback = () => {
    const returnValue = compileExpression(sourceCode);
  }

  return {
    ...action,
    callback,
  }
}

const processNotifyAction = ({ action, processedOptions: options }) => {
  const { notifyContent, notifyVariant, notifyHideIconVariant, notifyAnchorOrigin } = options;

  const origins = notifyAnchorOrigin.split('-');

  const anchorOrigin = {
    vertical: origins[0],
    horizontal: origins[1],
  };

  const callback = () => {
    enqueueSnackbar(notifyContent, {
      hideIconVariant: notifyHideIconVariant,
      variant: notifyVariant,
      anchorOrigin,
    })
  }

  return {
    ...action,
    callback,
  }
}

const processNavigationAction = ({ action, processedOptions: options }) => {
  const { navigationLink, navigationType, navigationFeature } = options;

  
  let feature = '';
  if (navigationFeature) {
    const sandbox = new Sandbox({ mode: 'expression' });
    sandbox.init({});
    const featureObj = sandbox.exec(navigationFeature);
    feature = Object.keys(featureObj).map(key => {
      const value = featureObj[key];
      return `${key}=${value}`;
    }).join(',');
  }
  const callback = () => {
    window.open(navigationLink, navigationType, feature);
  }

  return {
    ...action,
    callback,
  };
}

export const processAction = ({ id, eventId, type, action }) => {
  let processedAction = action;
  const processedOptions = resolveOptions({ id, eventId, type, action });
  switch (action.type) {
    case ActionType.CONTROL_COMPONENT:
      processedAction = processControlComponentAction({ id, eventId, type, action, processedOptions });
      break;
    case ActionType.RUN_SCRIPT:
      processedAction = processRunScriptAction({ id, eventId, type, action, processedOptions });
      break;
    case ActionType.NOTIFY:
      processedAction = processNotifyAction({ action, processedOptions });
      break;
    case ActionType.NAVIGATION:
      processedAction = processNavigationAction({ action, processedOptions });
      break;
    default:
      console.warn('unsupported action type', action.type);
      break;
  }
  return processedAction;
}

export const getActionDefaultOptions = (mountType, action) => {
  const componentInfo = useComponentStore.getState()[mountType];
  const { schema } = mountType;
  let options: any = {};
  switch (action.type) {
    case ActionType.RUN_SCRIPT:
      options = {
        sourceCode: 'console.log("hello there")',
      };
      break;
    case ActionType.CONTROL_COMPONENT:
      options = {

      };
    default:
      break;
  }

  return options;
}