import { CanvasPlugin, Message } from "../../type";
import AdaptivePluagin from "./adaptive";
import TipLayerPlugin from "./tipLayer";
import GridLayerPlugin from "./gridLayer";
import ShortcutPlugin from "./shortcut";
import MessageBus from "../../utils/messageBus";

class PluginCenter {

  messageBus: MessageBus = new MessageBus();

  plugins: any[] = [];

  register(plugin: CanvasPlugin) {
    // 
    const handers = plugin.beforeRegister?.(this.messageBus);
    this.plugins.push({
      Component: () => plugin.render.bind(plugin)(),
      id: plugin.id,
      handers,
      plugin,
    });
  }

  // TODO
  unregister(plugin: CanvasPlugin) {

  }

  publishMessage(channel: string, message: Message) {
    this.messageBus.publish(channel, message);
  }

  subscribe(channel: string, handler) {
    this.messageBus.subscribe(channel, handler);
  }

  unsubscribe(channel: string, handler) {
    this.messageBus.unsubscribe(channel, handler);
  }
}

const pluginCenter = new PluginCenter();

pluginCenter.register(new AdaptivePluagin());
pluginCenter.register(new TipLayerPlugin());
pluginCenter.register(new GridLayerPlugin());
pluginCenter.register(new ShortcutPlugin());

export default pluginCenter;