import React from 'react';
import { Tooltip as MUITooltip, TooltipProps } from '@mui/material';
import { ReactNode } from 'react';
import { ReactElement } from 'react';

interface IProps extends Pick<TooltipProps, 'title' | 'children' | 'placement' | 'arrow' | 'hidden'>{
  offset?: [offsetX: number, offsetY: number];
}

const Tooltip = (props: IProps) => {

  const { title, children, placement, arrow, offset = [], hidden } = props;

  return (
    <MUITooltip
      title={title}
      placement={placement}
      arrow={arrow}
      hidden={hidden}
      slotProps={{
        popper: {
          modifiers: [
            {
              name: 'offset',
              options: {
                offset,
              }
            }
          ]
        }
      }}
    >
      {children}
    </MUITooltip>
  )
}

export default Tooltip;