import { useRef } from "react"
import { cloneDeep, isEqual } from 'lodash-es';

const symbolMemoInit = Symbol('memoInit');

export const useShallowMemo = <T>(factory: () => T, deps: any[]): T => {
  const resultRef = useRef<any>(symbolMemoInit);
  const depsRef = useRef(deps);

  const isDepEqual = deps.every((dep, index) => isEqual(dep, depsRef.current[index]))

  if (resultRef.current === symbolMemoInit || !isDepEqual) {
    resultRef.current = factory();
  }

  depsRef.current = deps.map(dep => cloneDeep(dep));

  return resultRef.current;
}