import { Box } from "@mui/material";
import React from "react";
import useGridStyle from "../../hooks/useGridStyle";
import { useRef } from "react";
import { ComponentSchema, SchemaChange } from "../../type";
import { useDrop } from "react-dnd";
import { DragItemType, DragSourceType, EventType, LayoutType } from "../../constant";
import { generateComponentId, generateInitialState, getOffsetInRoot } from "../../misc";
import useDragStore from "../../drag/dragStore";
import useCanvasStore from "../../canvas/store";
import { collapseLayout } from "../../canvas/utils";
import evtManager from "../../event";
import { isChild, useSchemaStore } from "../../schema";
import useComponentStore from "../store";
import { useStatesStore } from "../../states";
import { compact, debounce, pickBy, throttle } from "lodash-es";
import { mountMethods } from "../../action/methods";
import { useEffect } from "react";
import { getChildren } from "../../schema/utils";
import { map } from "lodash-es";
import { useState } from "react";
import useGlobalStore from "../../globalStore";


const Container = props => {
  const { schemaNode = {}, children, display, _preview, columnCount, rowHeight, flexDirection, _isRoot } = props;
  const rootRef = useRef<HTMLElement>(null);

  const isResizing = useCanvasStore(state => state.isResizing);
  const isDragging = useDragStore(state => state.isDragging);
  const [keyMetaDown, setKeyMetaDown] = useState(false);
  const platformMode = useGlobalStore(state => state.mode);

  useEffect(() => {
    if (_preview || platformMode !== 'edit') {
      return;
    }
    const keyDownHandler = e => {
      if (e.key === 'Meta') {
        setKeyMetaDown(true);
      }
    }
    const keyUpHandler = e => {
      setKeyMetaDown(false);
    }
    const blurHandler = () => {
      setKeyMetaDown(false);
    }

    document.addEventListener('keydown', keyDownHandler);
    document.addEventListener('keyup', keyUpHandler);
    window.addEventListener('blur', blurHandler);
    return () => {
      document.removeEventListener('keydown', keyDownHandler);
      document.removeEventListener('keyup', keyUpHandler);
      window.removeEventListener('blur', blurHandler);
    }
  }, [_preview, platformMode]);

  const gridStyles = useGridStyle(true, rootRef.current, { columnCount, rowHeight });
  // console.log(gridStyles);
  const visibleGridStyles = {
    ...gridStyles,
    backgroundImage: `
          linear-gradient(to right, var(--canvas-gridline) 1px, transparent 1px),
          linear-gradient(to bottom, var(--canvas-gridline) 1px, transparent 1px)`,
  };

  if ((!isResizing && !isDragging && !keyMetaDown) || display !== 'grid' || _preview) {
    visibleGridStyles.visibility = 'hidden';
  }

  const changesRef = useRef([]);

  const dragItem = useDragStore(state => state.dragItem);
  const isDraggingSelf = dragItem?.id === schemaNode.id;

  const [collect, drop] = useDrop(() => ({
    accept: DragItemType.COMPONENT,
    hover: function (item: any, monitor) {
      if (item.id === schemaNode.id) {
        return
      }
      if (!monitor.isOver({ shallow: true })) {
        return;
      }
      useCanvasStore.setState({ previewScope: schemaNode.id })
      // console.log('container hover', monitor.isOver({ shallow: true }));
      const initialOffset = monitor.getInitialClientOffset();
      const sourceOffset = monitor.getInitialSourceClientOffset();
      const containerRoot = rootRef.current;
      if (!initialOffset || !sourceOffset || !containerRoot) {
        return;
      }
      // 
      const relativeX = initialOffset.x - sourceOffset.x;
      const relativeY = initialOffset.y - sourceOffset.y;
      const clientOffset = monitor.getClientOffset();

      if (!clientOffset) {
        return;
      }
      const gridItemWidth = containerRoot.offsetWidth / columnCount;
      const coordi = getOffsetInRoot({ x: Math.round(clientOffset.x - relativeX), y: Math.round(clientOffset.y - relativeY) }, containerRoot);
      const finalCoordi = {
        x: Math.min(Math.max(coordi.x, 0), containerRoot.offsetWidth - item.layout.width * gridItemWidth),
        y: Math.max(coordi.y, 0)
      };

      useDragStore.getState().setCoordi(finalCoordi);
      // 
      let grid: any = {
        x: Math.round(finalCoordi.x / gridItemWidth) + 1,
        y: Math.round(finalCoordi.y / rowHeight) + 1
      };
      // ,
      if (coordi.x < 0) {
        grid.width = Math.max(item.layout.width - Math.round(Math.abs(coordi.x / gridItemWidth)), 1);
      }
      // ,
      const overflowRightWidth = coordi.x + item.layout.width * gridItemWidth - gridItemWidth * columnCount;
      const overflowRightColumn = Math.round(overflowRightWidth / gridItemWidth);
      if (overflowRightColumn > 0) {
        const overColumnCount = Math.min(item.layout.width - 1, overflowRightColumn);
        grid.width = item.layout.width - overColumnCount;
        grid.x += overColumnCount;
      }

      let nextLayout = {
        ...item.layout,
        ...grid,
      };

      const lastLayout = useCanvasStore.getState().previewLayout;
      const previewScope = useCanvasStore.getState().previewScope;
      const [finalLayout, changes] = collapseLayout(nextLayout, lastLayout, previewScope);
      changesRef.current = changes;
      useCanvasStore.getState().setPreviewLayout(finalLayout);
      useCanvasStore.setState({ previewScope: schemaNode.id });

      evtManager.emit(EventType.DRAGGING, { item, coordi, clientOffset, on: schemaNode.id });
    },
    canDrop(item, monitor) {
      // console.log('container canDrop', item)
      return !useCanvasStore.getState().inserting
        && monitor.isOver({ shallow: true })
        && item.id !== schemaNode.id;
    },
    drop: (item: any, monitor) => {
      if (monitor.getItemType() !== DragItemType.COMPONENT) {
        console.error('wrong type', item);
      }
      if (!item.type) {
        console.error('dropItem type required');
        return;
      }

      const grid = useCanvasStore.getState().previewLayout;
      const { appendChild, applyChanges } = useSchemaStore.getState();

      if (item.source === DragSourceType.CANVAS) {
        const { updateNode, moveChild } = useSchemaStore.getState();
        if (!isChild(item.id, schemaNode.id)) {
          moveChild(item.id, schemaNode.id);
        }
        updateNode(item.id, { layout: { ...grid, inSlot: schemaNode.id } });
      }

      if (item.source === DragSourceType.COMPONENT_PANEL) {
        const components = useComponentStore.getState();
        const setState = useStatesStore.getState().setState;

        const actualComponents = pickBy(components, component => typeof component !== 'function');
        const componentInfo = actualComponents[item.type];
        if (!componentInfo) {
          console.error('component not found', item);
        }

        const componentSchema = componentInfo.schema;
        const initialState = generateInitialState(componentSchema);
        const id = generateComponentId(componentInfo.type);
        item.id = id;

        const newSchemaNode = {
          id,
          debugging: componentInfo.mark === 'debug',
          type: componentInfo.type,
          state: initialState,
          layout: {
            type: LayoutType.GRID,
            ...grid,
            // width: componentSchema.layout.width,
            // height: componentSchema.layout.height,
          }
        };

        setState(id, initialState);
        appendChild(schemaNode.id, newSchemaNode);
        mountMethods(newSchemaNode.id);
      }
      if (changesRef.current) {
        applyChanges(changesRef.current);
      }
      evtManager.emit(EventType.DROP, { item: { ...item }, target: schemaNode.id, prevLayout: item.layout, layout: { ...grid } });
    },
  }), [columnCount, rowHeight, schemaNode.id]);

  // 
  useEffect(() => {
    const children = getChildren(schemaNode.id);
    const changes: SchemaChange[] = compact(map(children, child => {
      if (child.layout.x > columnCount || child.layout.x + child.layout.width > columnCount + 1) {
        return {
          target: child.id,
          type: 'update',
          payload: {
            layout: {
              ...child.layout,
              x: Math.min(columnCount, child.layout.x),
              width: Math.min(columnCount + 1 - child.layout.x, child.layout.width),
            }
          }
        } as SchemaChange;
      }
    }));
    useSchemaStore.getState().applyChanges(changes);
  }, [columnCount, schemaNode.id]);

  return (
    <Box
      height={'100%'}
    >
      <Box
        ref={rootRef}
        // height={'100%'}
        position={'relative'}
        className="drop-area-container"
        id={`drop-area-container-${schemaNode.id}`}
        bgcolor={'var(--bg-0)'}
      // boxShadow={'2px 2px 5px #cccccc'}
        border={_isRoot ? '' : '1px solid var(--border-1)'}
      >
        <Box
          ref={!isDraggingSelf ? drop as any : null}
          style={{
            height: '100%',
            ...gridStyles,
            position: 'relative',
            ...(schemaNode.style) || {},
            display,
            flexDirection,
          }}
          sx={{
            zIndex: 7,
            '&>*': {
              flex: 1,
            }
          }}
          className="drop-area"
          id={`drop-area-${schemaNode.id}`}
        >
          {children}
        </Box>
        <Box
          sx={{
            ...visibleGridStyles,
            pointerEvents: 'none',
          }}
          className="grid-display-area-visible"
          height={'100%'}
          width={'100%'}
          position={'absolute'}
          zIndex={5}
          top={0}
          left={0}
        >
        </Box>
        <Box
          sx={{
            ...gridStyles,
            pointerEvents: 'none',
          }}
          className="grid-display-area"
          id={`grid-display-area-${schemaNode.id}`}
          height={'100%'}
          width={'100%'}
          position={'absolute'}
          zIndex={10}
          top={0}
          left={0}
        >
          {/*  portal */}
        </Box>
      </Box>
    </Box>
  )
}

const schema: ComponentSchema = {
  type: 'container',
  name: '',
  attributes: [
    {
      name: '',
      key: 'display',
      defaultValue: 'grid',
      widget: 'buttons',
      accept: 'string',
      widgetProps: {
        options: [
          {
            value: 'grid',
            label: '',
          },
          {
            value: 'flex',
            label: ''
          }
        ]
      }
    },
    {
      name: '',
      key: 'columnCount',
      defaultValue: 5,
      widget: 'input',
      accept: 'number',
      hidden: state => state.display !== 'grid',
      widgetProps: {
        type: 'number',
        inputProps: {
          min: 1,
          max: 20,
        }
      }
    },
    {
      name: '',
      key: 'rowHeight',
      defaultValue: 10,
      widget: 'input',
      hidden: state => state.display !== 'grid',
      accept: 'number',
      widgetProps: {
        type: 'number',
        inputProps: {
          min: 5,
        },
      }
    },
    {
      name: '',
      key: 'flexDirection',
      defaultValue: 'row',
      widget: 'buttons',
      accept: 'string',
      hidden: states => states.display !== 'flex',
      widgetProps: {
        options: [
          {
            value: 'row',
            label: '',
          },
          {
            value: 'column',
            label: ''
          }
        ]
      }
    }
  ],
  layout: {
    height: 20,
    width: 3
  }
}

Object.assign(Container, { schema });

export default Container;