import React, { useEffect, useState } from "react";
import { CanvasPlugin } from "../../type";
import useDragStore from "../../drag/dragStore";
import useCanvasStore from "../store";
import useGridStyle from "../../hooks/useGridStyle";
import { CSSProperties } from "react";
import { createPortal } from "react-dom";
import styles from './index.css';
import { MessageBusChannel, Theme } from "../../constant";
import useCanvasRect from "../../hooks/useCanvasLayout";
import { shouldRestrict } from "../utils";
import { useShallow } from 'zustand/react/shallow';
import MessageBus from "../../utils/messageBus";
import { useReducer } from "react";
import { isEmpty, omit } from "lodash-es";
import { useMemo } from "react";
import { getDropArea, getGridDispayArea } from "../../misc";
import { useStatesStore } from "../../states";

class GridLayerPlugin implements CanvasPlugin {

  id: string = 'gridLayer';

  visibility = false;

  forceUpdate;

  // throtte start ui
  lastPreviewScope = '';

  cachedPreviewElement: any = null;

  throtteBeginAt = 0;

  throtteDuration = 100;

  throtteTailTimer: any = 0;
  // throtte end

  beforeRegister(messageBus: MessageBus) {
    messageBus.subscribe(MessageBusChannel.CANVAS_PLUGIN_UPDATE_UI, (message) => {
      const { type, source, payload } = message;
      if (type === 'updateVisibility' && payload.target === this.id) {
        this.visibility = !!payload.value;
        this.forceUpdate();
      }
    });
  }

  shouldLaunchThrotte(previewScope) {
    try {
      return this.lastPreviewScope !== previewScope && this.cachedPreviewElement
    } finally {
      this.lastPreviewScope = previewScope;
    }
  }

  renderGridItems(isDragging: boolean, dragItem: any) {
    const [gridArea, setGridArea] = useState<any>('');

    const { previewLayout, isResizing, resizeItem, previewScope } = useCanvasStore(useShallow(state => ({
      previewLayout: state.previewLayout,
      isResizing: state.isResizing,
      resizeItem: state.resizeItem,
      previewScope: state.previewScope,
    })));
    // const canvasLayout = useCanvasRect(wipDropArea);

    const inserting = useCanvasStore(state => state.inserting);

    const { display } = useStatesStore(useShallow(state => ({
      display: state[previewScope]?.display
    })));

    useEffect(() => {
      if ((!isDragging || !dragItem) && (!isResizing || !resizeItem)) {
        setGridArea('');
        return;
      }

      const grid = previewLayout;

      if (isNaN(grid.y) || isNaN(grid.x)) {
        setGridArea('');
        return;
      }

      const originLayout = dragItem?.layout || resizeItem?.layout || {};

      const nextLayout = {
        ...originLayout,
        ...grid,
      }
      if (shouldRestrict(nextLayout)) {
        return;
      }

      setGridArea(`${grid.y} / ${grid.x} / span ${nextLayout.height} / span ${nextLayout.width}`);
    }, [isDragging, dragItem, previewLayout, isResizing]);

    const portalTo = getGridDispayArea(previewScope);

    if (!(isDragging || isResizing) || !gridArea) {
      return null;
    }

    if (inserting) {
      setGridArea('');
      return null;
    }

    if (performance.now() < this.throtteBeginAt + this.throtteDuration) {
      return this.cachedPreviewElement;
    }
    clearTimeout(this.throtteTailTimer);
    if (this.shouldLaunchThrotte(previewScope)) {
      this.throtteBeginAt = performance.now();
      this.throtteTailTimer = setTimeout(() => {
        console.log('ui, ' );
        this.forceUpdate();
      }, this.throtteDuration + 100);
      return this.cachedPreviewElement;
    }

    const dragItemStyle: CSSProperties = {
      gridArea,
      backgroundColor: Theme.CANVAS_PREVIEW_GRID,
      pointerEvents: 'none',
      position: 'relative',
      display: 'flex',
      justifyContent: 'center',
      borderTop: '2px solid var(--primary-color)',
      zIndex: 20
    };


    const element = (
      <div style={dragItemStyle} />
    )

    if (!portalTo) {
      return null;
    }

    if (display !== 'grid') {
      return null;
    }


    return createPortal(element, portalTo);
  }

  render() {
    const isDragging = useDragStore(state => state.isDragging);
    const isResizing = useCanvasStore(state => state.isResizing);
    const dragItem = useDragStore(state => state.dragItem);

    useEffect(() => {
      const blurHandler = () =>{
         this.visibility = false;
         this.forceUpdate();
      }

      window.addEventListener('blur', blurHandler);
      return () => window.removeEventListener('blur', blurHandler);
    }, []);

    const [_, forceUpdate] = useReducer(x => x + 1, 0);
    this.forceUpdate = forceUpdate;

    const gridItems = this.renderGridItems(isDragging, dragItem);

    this.cachedPreviewElement = gridItems;

    return <>
      {gridItems}
    </>
  }
}

export default GridLayerPlugin;