import { ReactNode } from "react";
import { CanvasPlugin, Message } from "../../type";
import { createPortal } from "react-dom";
import React from "react";
import styles from './index.css';
import useGridStyle from "../../hooks/useGridStyle";
import { useSchemaStore } from "../../schema";
import useCanvasStore from "../store";
import { CSSProperties } from "react";
import useCanvasRect from "../../hooks/useCanvasLayout";
import { GRID_COLUMN_COUNT, GRID_ROW_HEIGHT, MessageBusChannel, RESIZE_EDGE_SIZE, RESIZE_SQUARE_SIZE, Theme } from "../../constant";
import { useEffect } from "react";
import { getDropArea, getGridItemRowHeight, getGridItemWidth, getGridItemWidthV2, getOffsetInRoot, getRootId, transferCoordiToGrid, transferGridPointsToLayout, transferLayoutToGridPoints } from "../../misc";
import { useShallow } from 'zustand/react/shallow';
import { compact } from 'lodash-es';
import useDragStore from "../../drag/dragStore";
import { collapseLayout, shouldRestrict } from "../utils";
import { useShallowMemo } from "../../hooks/common";
import { useState } from "react";
import { useReducer } from "react";
import MessageBus from "../../utils/messageBus";

class TipLayerPlugin implements CanvasPlugin {

  id: string = 'tipLayer';

  cursors = {
    top: 'ns-resize',
    bottom: 'ns-resize',
    left: 'ew-resize',
    right: 'ew-resize',
    topLeft: 'nwse-resize',
    topRight: 'nesw-resize',
    bottomRight: 'nwse-resize',
    bottomLeft: 'nesw-resize',
  }

  forceUpdate: any

  messageHandlers: any[] = [];

  beforeRegister(messageBus: MessageBus) {
    const uiChannelHandler = (message: Message) => {
      if (message.type === 'flushUI') {
        this.forceUpdate?.();
      }
    }
    messageBus.subscribe(MessageBusChannel.CANVAS_PLUGIN_UPDATE_UI, uiChannelHandler);
    this.messageHandlers.push(uiChannelHandler);
  }

  renderSlotHint(node) {
    const isResizing = useCanvasStore(state => state.isResizing);
    if (isResizing) {
      return null;
    }
    if (!node) {
      return null;
    }
    const { id } = node;
    const containerDOM: HTMLDivElement = useCanvasStore.getState().containers[id];
    if (!containerDOM) {
      return null;
    }
    const containerRect = containerDOM.getBoundingClientRect();
    const posInRoot = getOffsetInRoot(containerRect);

    const style: CSSProperties = {
      top: posInRoot.y,
      left: posInRoot.x,
      width: containerRect.width,
      height: containerRect.height,
      position: 'absolute',
      background: 'rgba(20, 40, 150, 0.3)',
      zIndex: 10,
      pointerEvents: 'none',
      boxSizing: 'border-box',
    }

    return <div style={style} />
  }

  renderHovered(hoveredNode) {
    const isResizing = useCanvasStore(state => state.isResizing);
    if (isResizing) {
      return null;
    }
    if (!hoveredNode) {
      return null;
    }
    const { id } = hoveredNode;
    const containerDOM: HTMLDivElement = useCanvasStore.getState().containers[id];
    if (!containerDOM) {
      return null;
    }
    const containerRect = containerDOM.getBoundingClientRect();
    const posInRoot = getOffsetInRoot(containerRect);

    const style: CSSProperties = {
      top: posInRoot.y,
      left: posInRoot.x,
      width: containerRect.width,
      height: containerRect.height,
      position: 'absolute',
      zIndex: 10,
      pointerEvents: 'none',
      border: `1px dashed ${Theme.CANVAS_HOVERED}`,
      boxSizing: 'border-box',
    };

    return (
      <div style={style} />
    )
  }

  getNextLayout(anchorType, coordi, lastLayout) {
    const { previewScope } = useCanvasStore.getState();
    const gridItemWidth = getGridItemWidthV2(previewScope);
    const gridRowHeight = getGridItemRowHeight(previewScope);
    // coordi
    const resizePoint = transferCoordiToGrid(coordi, gridItemWidth, gridRowHeight);
    const lastLayoutPoints = transferLayoutToGridPoints(lastLayout);
    const newPoints: any = { };

    const { bottomLeft, bottomRight, topLeft, topRight } = lastLayoutPoints;

    const minHeight = Math.max(lastLayout.minHeight || 1, 1);
    const minWidth = Math.max(lastLayout.minWidth || 1, 1);

    switch(anchorType) {
      case 'top':
        newPoints.bottomRight = bottomRight;
        newPoints.topLeft = {
          x: topLeft.x,
          y: Math.min(resizePoint.y, bottomLeft.y - minHeight),
        };
        break;
      case 'right':
        newPoints.bottomLeft = bottomLeft;
        newPoints.topRight = {
          x: Math.max(resizePoint.x, topLeft.x + minWidth),
          y: topRight.y,
        };
        break;
      case 'bottom':
        newPoints.topLeft = topLeft;
        newPoints.bottomRight = {
          x: bottomRight.x,
          y: Math.max(topLeft.y + minHeight, resizePoint.y),
        };
        break;
      case 'left': 
        newPoints.bottomRight = bottomRight;
        newPoints.topLeft = {
          x: Math.min(bottomRight.x - minWidth, resizePoint.x),
          y: topLeft.y,
        };
        break;
      case 'bottomRight':
        newPoints.topLeft = topLeft;
        const newBottomRight = { x: Math.max(topLeft.x + minWidth, resizePoint.x), y: Math.max(topLeft.y + minHeight, resizePoint.y) };
        newPoints.bottomRight = newBottomRight;
        break;
      case 'topLeft':
        newPoints.bottomRight = bottomRight;
        const newTopLeft = { x: Math.min(bottomRight.x - minWidth, resizePoint.x), y: Math.min(bottomRight.y - minHeight, resizePoint.y) };
        newPoints.topLeft = newTopLeft;
        break;
      case 'topRight':
        newPoints.bottomLeft = bottomLeft;
        const newTopRight = { x: Math.max(bottomLeft.x + minWidth, resizePoint.x), y: Math.min(bottomLeft.y - minHeight, resizePoint.y) };
        newPoints.topRight = newTopRight;
        break;
      case 'bottomLeft':
        newPoints.topRight = topRight;
        const newBottomLeft = { x: Math.min(topRight.x - minWidth, resizePoint.x), y: Math.max(topRight.y + minHeight, resizePoint.y) };
        newPoints.bottomLeft = newBottomLeft;
        break;
      default:
        console.warn('resize anchorType not supported yet', anchorType);
    }

    const nextLayout = transferGridPointsToLayout(newPoints);
    if (shouldRestrict(nextLayout)) {
      return lastLayout;
    }
    return nextLayout;
  }

  renderResizeAnchor(selectedNode) {
    const isResizing = useCanvasStore(state => state.isResizing);
    const markResizeStart = useCanvasStore(state => state.markResizeStart);
    const markResizeEnd = useCanvasStore(state => state.markResizeEnd);
    const resizeItem = useCanvasStore(state => state.resizeItem);
    const updateSchemaNode = useSchemaStore(state => state.updateNode);
    const selectedLayout = useSchemaStore(state => state.plainSchema[selectedNode?.id]?.layout);
    const [uniCursor, setUniCursor] = useState('');
    const setPreviewLayout = useCanvasStore(state => state.setPreviewLayout);

    const memoLayout = useShallowMemo(() => {
      return { ...selectedLayout }
    }, [selectedLayout]);

    const resizeStart = (e: MouseEvent, anchorType) => {
      e.preventDefault();
      const canvasContainerInnerElement = document.getElementById('canvas-container-inner');
      if (isResizing || !canvasContainerInnerElement) {
        return;
      }
      markResizeStart({
        id: selectedNode.id,
        name: selectedNode.name,
        type: selectedNode.type,
        anchorType,
        layout: {
          ...selectedLayout,
        },
      });

      canvasContainerInnerElement.style.cursor = this.cursors[anchorType];
      setUniCursor(this.cursors[anchorType]);
    };

    useEffect(() => {
      const canvasContainerInnerElement = document.getElementById('canvas-container-inner');
      if (!isResizing || !canvasContainerInnerElement) {
        return;
      }
      const { previewScope } = useCanvasStore.getState();
      const scopeDOM = getDropArea(previewScope) || canvasContainerInnerElement ;
      const onMouseMove = (e: MouseEvent) => {
        if (!isResizing) {
          return;
        }
        const coordi = getOffsetInRoot({ x: e.clientX, y: e.clientY }, scopeDOM);
        const nextLayout = this.getNextLayout(resizeItem.anchorType, coordi, memoLayout);
        const [newNextLayout, changes] = collapseLayout(nextLayout, { ...nextLayout }, previewScope);
        setPreviewLayout(newNextLayout);
        updateSchemaNode(resizeItem.id, { layout: newNextLayout });
        useSchemaStore.getState().applyChanges(changes);
      }
      const onMouseUp = (e: MouseEvent) => {
        if (isResizing) {
          markResizeEnd();
          setUniCursor('');
          if (canvasContainerInnerElement) {
            canvasContainerInnerElement.style.cursor = '';
          }
        }
      }
      // canvasContainerInnerElement.addEventListener('mousemove', onMouseMove);
      // canvasContainerInnerElement.addEventListener('mouseup', onMouseUp);
      canvasContainerInnerElement.addEventListener('pointermove', onMouseMove);
      canvasContainerInnerElement.addEventListener('pointerup', onMouseUp);
      return () => {
        // canvasContainerInnerElement.removeEventListener('mousemove', onMouseMove);
        // canvasContainerInnerElement.removeEventListener('mouseup', onMouseUp);
        canvasContainerInnerElement.removeEventListener('pointermove', onMouseMove);
        canvasContainerInnerElement.removeEventListener('pointerup', onMouseUp);
      }
    }, [isResizing, resizeItem, markResizeEnd, memoLayout, updateSchemaNode]);

    if (!selectedNode) {
      return null;
    }
    const containerDOM: HTMLDivElement = useCanvasStore.getState().containers[selectedNode.id];
    if (!containerDOM) {
      return null;
    }
    // if (selectedNode.layout?.inSlot) {
    //   return null;
    // }
    const containerRect = containerDOM.getBoundingClientRect();
    const width = containerRect.width - 2;
    const height = containerRect.height - 2
    const commonStyle: CSSProperties = {
      width: RESIZE_SQUARE_SIZE,
      height: RESIZE_SQUARE_SIZE,
      background: Theme.CANVAS_RESIZE_ANCHOR,
    }

    const actualAnchorSize = 10;
    const fineFactor = actualAnchorSize / 2;

    if (selectedNode.id === getRootId()) {
      return null;
    }

    return (
      <>
        <div className={styles['resize-anchor']} onPointerDown={(e) => resizeStart(e as any, 'topLeft')} style={{ top: 0 - fineFactor, left: 0 - fineFactor, cursor: uniCursor || 'nwse-resize' }}>
          <div className={styles['resize-anchor-displayer']} style={commonStyle} />
        </div>
        <div className={styles['resize-anchor']} onPointerDown={(e) => resizeStart(e as any, 'topRight')} style={{ top: 0 - fineFactor, left: width - fineFactor, cursor: uniCursor || 'nesw-resize' }}>
          <div className={styles['resize-anchor-displayer']} style={commonStyle} />
        </div>
        <div className={styles['resize-anchor']} onPointerDown={(e) => resizeStart(e as any, 'bottomRight')} style={{ top: height - fineFactor, left: width - fineFactor, cursor: uniCursor || 'nwse-resize' }}>
          <div className={styles['resize-anchor-displayer']} style={commonStyle} />
        </div>
        <div className={styles['resize-anchor']} onPointerDown={(e) => resizeStart(e as any, 'bottomLeft')} style={{ top: height - fineFactor, left: 0 - fineFactor, cursor: uniCursor || 'nesw-resize' }}>
          <div className={styles['resize-anchor-displayer']} style={commonStyle} />
        </div>

        <div 
          className={styles['resize-border-horizontal']}
          style={{
            top: -RESIZE_EDGE_SIZE,
            left: 0,
            width,
            height: RESIZE_EDGE_SIZE,
            cursor: uniCursor || 'ns-resize'
          }}
          // onMouseDown={e => resizeStart(e as any, 'top')}
          onPointerDown={(e) => resizeStart(e as any, 'top')} 
        />
        <div 
          className={styles['resize-border-horizontal']}
          style={{
            top: height,
            left: 0,
            width,
            height: RESIZE_EDGE_SIZE,
            cursor: uniCursor || 'ns-resize'
          }}
          // onMouseDown={e => resizeStart(e as any, 'bottom')}
          onPointerDown={(e) => resizeStart(e as any, 'bottom')} 
        />
        <div 
          className={styles['resize-border-vertical']}
          style={{
            top: 0,
            left: -RESIZE_EDGE_SIZE,
            height,
            width: RESIZE_EDGE_SIZE,
            cursor: uniCursor || 'ew-resize'
          }}
          // onMouseDown={e => resizeStart(e as any, 'left')}
          onPointerDown={(e) => resizeStart(e as any, 'left')} 
        />
        <div 
          className={styles['resize-border-vertical']}
          style={{
            top: 0,
            left: width,
            height,
            width: RESIZE_EDGE_SIZE,
            cursor: uniCursor || 'ew-resize'
          }}
          // onMouseDown={e => resizeStart(e as any, 'right')}
          onPointerDown={(e) => resizeStart(e as any, 'right')} 
        />
      </>
    )
  }

  renderSelected(selectedNode) {
    const resizeAnchorChild = this.renderResizeAnchor(selectedNode);
    if (!selectedNode) {
      return null;
    }
    const { id } = selectedNode;
    const containerDOM: HTMLDivElement = useCanvasStore.getState().containers[id];
    if (!containerDOM) {
      return null;
    }
    const containerRect = containerDOM.getBoundingClientRect();
    const posInRoot = getOffsetInRoot(containerRect);

    const style: CSSProperties = {
      top: 0,
      left: 0,
      willChange: 'transform, width, height',
      transform: `translateX(${posInRoot.x}px) translateY(${posInRoot.y}px)`,
      width: containerRect.width,
      height: containerRect.height,
      position: 'absolute',
      zIndex: 10,
      pointerEvents: 'none',
      border: `1px solid ${Theme.CANVAS_SELECTED}`,
      boxSizing: 'border-box',
    };

    return (
      <div style={style}>
        {resizeAnchorChild}
      </div>
    )
  }

  renderOutlines() {
    const isDragging = useDragStore(state => state.isDragging);
    const dragItem = useDragStore(state => state.dragItem);
    const isResizing = useCanvasStore(state => state.isResizing);
    const resizeItem = useCanvasStore(state => state.resizeItem);
    const inserting = useCanvasStore(state => state.inserting);
    const canvasChildren = useSchemaStore(useShallow(state => state.schema.children));
    const containers = useCanvasStore(state => state.containers);
    if (!canvasChildren?.length || (!isDragging && !isResizing)) {
      return null;
    }
    const paintingContainers: any[] = compact(canvasChildren.map(child => {
      if (inserting === child.id || dragItem?.id === child.id || resizeItem?.id === child.id) {
        return null;
      }
      return containers[child.id];
    }));

    return (
      <>
        {paintingContainers.map((container: HTMLElement) => {
          const containerRect = container.getBoundingClientRect();
          const posInRoot = getOffsetInRoot(containerRect);
          const style: CSSProperties = {
            top: posInRoot.y,
            left: posInRoot.x,
            width: containerRect.width,
            height: containerRect.height,
            position: 'absolute',
            zIndex: 10,
            pointerEvents: 'none',
            border: `1px dashed ${Theme.CANVAS_HOVERED}`,
            boxSizing: 'border-box',
          };

          return <div style={style} key={container.id} />
        })}
      </>
    )
  }

  renderLabel(selectedNode, hoveredNode) {
    const isResizing = useCanvasStore(state => state.isResizing);
    const containers = useCanvasStore(state => state.containers);
    if (isResizing) {
      return null;
    }
    const keys = ['selected', 'hovered'];
    return [selectedNode, hoveredNode].map((node, index) => {
      if (!node) {
        return null;
      }
      const container = containers[node.id];
      if (!container) {
        return null;
      }
      const containerRect = container.getBoundingClientRect();
          const posInRoot = getOffsetInRoot(containerRect);
          const style: CSSProperties = {
            top: posInRoot.y - 20,
            left: posInRoot.x,
            position: 'absolute',
            zIndex: 10,
            pointerEvents: 'none',
            borderRadius: '3px',
            overflow: 'hidden',
          };

          return (
            <div style={style} key={keys[index]}>
              <div style={{
                padding: '2px 5px',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                background: 'rgba(24,148,225)',
              }}>
                <span
                  style={{
                    fontSize: '12px',
                    lineHeight: '14px',
                    color: '#fff'
                  }}
                >
                  {node.id}
                </span>
              </div>
            </div>
          )
    });
  }

  render() {
    const gridStyles = useGridStyle(false);
    const plianSchema = useSchemaStore(state => state.plainSchema);
    const selected = useCanvasStore(state => state.selected);
    const hovered = useCanvasStore(state => state.hovered);
    const inserting = useCanvasStore(state => state.inserting);

    useCanvasRect(getDropArea(getRootId()));

    useCanvasStore(useShallow(state => {
      const { containers } = state;
      const selectedDOM: HTMLElement = containers[selected];
      const hoveredDOM: HTMLElement = containers[hovered];
      const selectedRect = selectedDOM?.getBoundingClientRect() || {};
      const hoveredRect = hoveredDOM?.getBoundingClientRect() || {};
      return {
        selectedX: selectedRect.x,
        selectedY: selectedRect.y,
        selectedWidth: selectedRect.width,
        selectedHeight: selectedRect.height,
        hoveredX: hoveredRect.x,
        hoveredY: hoveredRect.y,
        hoveredWidth: hoveredRect.width,
        hoveredHeight: hoveredRect.height,
      };
    }))

    const [_, forceUpdate] = useReducer(x => x + 1, 0);
    this.forceUpdate = forceUpdate;

    const canvasContainerElement = document.getElementById('canvas-content-root');

    const selectedNode = plianSchema[selected];
    const hoveredNode = plianSchema[hovered];
    const insertingNode = plianSchema[inserting];

    const selectedChild = this.renderSelected(selectedNode);
    const hoveredChild = this.renderHovered(hoveredNode);
    const slotHindChild = this.renderSlotHint(insertingNode);
    const outlinesChild = this.renderOutlines();
    const labelChild = this.renderLabel(selectedNode, hoveredNode);

    if (!canvasContainerElement) {
      return null;
    }

    return createPortal((
      <div 
        className={styles['canvas-layer']}
        style={gridStyles}
      >
        {hoveredChild}
        {selectedChild}
        {slotHindChild}
        {outlinesChild}
        {labelChild}
      </div>
    ), canvasContainerElement)
  }
}

export default TipLayerPlugin;
