import useComponentStore from "../components/store"
import { reduce, map } from 'lodash-es';
import { useSchemaStore } from "../schema";
import { getSchemaByType, now } from "../misc";

const methods: any = {}
const forceUpdateMethods: any = {};

export function mountMethods(input) {
  const schemaNode = typeof input === 'string' ? useSchemaStore.getState().plainSchema[input] : input;
  const id = schemaNode.id;
  const componentInfo = useComponentStore.getState()[schemaNode.type];
  if (!componentInfo) {
    return;
  }
  const { schema } = componentInfo;
  const { methods: _methods } = schema;
  if (!_methods?.length) {
    return;
  }

  const componentMethods = reduce(_methods, (acc, curr: any) => {
    const currCopy = { ...curr };
    currCopy.callback = (context) => {
      if (!curr.handler) {
        console.warn('method requires handler', curr);
        return;
      }
      try {   
        return curr.handler(context);
      } catch (error) {
       console.error('method exec error', curr, error);
      }
    }
    acc[curr.type] = currCopy;
    return acc;
  }, {});

  methods[id] = componentMethods;
}

export const getMethods = () => methods;

export const getComponentMethods = (id) => methods[id];

export const getMethodOptions = (type: string) => {
  const schema = getSchemaByType(type);
  return map(schema.methods, method => ({
    label: method.name,
    value: method.type,
  }));
}

export const unmountMethods = (id) => {
  delete methods[id];
  delete forceUpdateMethods[id];
}

export const setForceUpdate = (id, forceUpdate) => {
  forceUpdateMethods[id] = {
    callback: forceUpdate,
    updatedAt: now(),
  }
}

export const forceUpdate = (id) => {
  forceUpdateMethods[id]?.callback?.();
}

Object.defineProperty(window, '__METHODS__', {
  get() {
    return methods;
  },
});

Object.defineProperty(window, '__UPDATE_METHODS__', {
  get() {
    return forceUpdateMethods;
  },
});