import React, { CSSProperties, useCallback, useState } from "react";
import { Menu, MenuItem } from '@mui/material';

interface IProps {
    menuItems: Array<{
        label: React.ReactNode;
        key: string;
        onClick: () => void;
        icon?: React.ReactNode;
        disabled?: boolean;
        hidden?: boolean;
    }>
    children: React.ReactNode;
    className?: string;
    style?: CSSProperties;
    onDisplay?: () => void;
    closeAfterClick?: boolean;
}

const ContextMenu = (props: IProps) => {
    const { menuItems, children, className, style, onDisplay, closeAfterClick = true } = props;


    const [menuOpen, setMenuOpen] = useState(false);
    const [menuPos, setMenuPos] = useState<any>(null);

    const handleContextMenu = (event: React.MouseEvent) => {
        event.preventDefault();
        event.stopPropagation();
        if (!menuOpen) {
            onDisplay?.();
            setMenuOpen(true);
            setMenuPos({
                x: event.clientX + 2,
                y: event.clientY - 6,
            });
        } else {
            setMenuOpen(false);
        }
    };
    const handleClose = useCallback(() => {
        setMenuOpen(false);
    }, []);

    return (
        <div onContextMenu={handleContextMenu} className={className} style={style}>
            {children}
            <Menu
                open={menuOpen}
                onClose={handleClose}
                anchorReference="anchorPosition"
                anchorPosition={menuOpen ? {
                    top: menuPos?.y,
                    left: menuPos?.x,
                } : undefined}
            >
                {menuItems.map(item => {
                    if (item.hidden) {
                        return null;
                    }
                    return (
                        <MenuItem
                            key={item.key}
                            onClick={() => {
                                item?.onClick();
                                if (closeAfterClick) {
                                    setMenuOpen(false);
                                }
                            }}
                            disabled={item.disabled}
                        >
                            {item.label}
                        </MenuItem>
                    )
                })}
            </Menu>
        </div>
    );
}

export default ContextMenu;