import { generateSafeContext, getSchemaById, isDev, makeReadOnly } from "../misc";
import FunctionSandBox from "../sandbox/function";
import { useSchemaStore } from "../schema";
import { useStatesStore } from "../states";
import useGlobalStore from "../globalStore";

function getExtraContext() {

  const extraContext = {
    current_user: {},
  };

  // Object.defineProperties(extraContext, {
  //   theme: {
  //     get() {
  //       return useGlobalStore.getState().theme;
  //     },
  //   }
  // });

  Object.defineProperties(extraContext.current_user, {
    username: {
      get() {
        return useGlobalStore.getState().userInfo.username;
      },
    }
  });

  return extraContext;
}

let expressionContext: any = getExtraContext();
const expressionSandBox = new FunctionSandBox({ mode: 'expression' });
let deps: any[] = [];

export function getExpressionContext() {
  return makeReadOnly(expressionContext);
}

export function updateExpressionContext() {
  const { plainSchema } = useSchemaStore.getState();

  expressionContext = getExtraContext();
  Object.values(plainSchema).forEach((node: any) => {
    const schema = getSchemaById(node.id);
    const subContext = {};
    schema.attributes?.forEach(attri => {
      const { key } = attri;
      Object.defineProperty(subContext, key, {
        get() {
          deps.push({ id: node.id, key });
          return useStatesStore.getState()[node.id]?.[key];
        },
      })
    });
    expressionContext[node.id] = subContext;
  });


  const sandboxContext = generateSafeContext(expressionContext);
  console.log('sfae', sandboxContext);
  expressionSandBox.init(sandboxContext);
}

export function compileExpression(expression: string) {
  try {
    deps = [];
    const expressionRegExp = /\{\{([\s\S]*?)\}\}/ig;
    let match: RegExpExecArray | null = null;
    let returnValue = expression;
    // 
    let isPure = false;
    const expressionRecords: any[] = [];
    while ((match = expressionRegExp.exec(expression)) !== null) {
      const exp = match[1];
      const expressionWithBracket = match[0];
      let expressionReturn = exp;
      let err;
      try {
        expressionReturn = expressionSandBox.exec(exp) || '';
      } catch (error) {
        if (isDev()) {
          console.error('compileExpression error', error);
        }
        // 
        expressionReturn = '';
        err = error;
      }
      expressionRecords.push({
        exp,
        expressionWithBracket,
        expressionReturn,
        index: match.index,
        err,
      });
      if (expressionWithBracket === match.input) {
        isPure = true;
        returnValue = expressionReturn;
        break;
      }
    }
    if (expressionRecords.length && !isPure) {
      expressionRecords.reverse().forEach(record => {
        if (record.err) {
          return;
        }
        const prefixStr = returnValue.substring(0, record.index);
        const sufixStr = returnValue.substring(record.index + record.expressionWithBracket.length, returnValue.length);
        returnValue = prefixStr + record.expressionReturn + sufixStr;
      });
    }
    expressionRegExp.lastIndex = 0;
    return [returnValue, [...deps], expressionRecords];
  } catch (error) {
    console.error('expression compile error', error);
    return [expression];
  } finally {
    deps = [];
  }
}

Object.defineProperty(window, '__EXP_CTX__', {
  get() {
    return expressionContext;
  },
})