import { Box } from "@mui/material"
import React from "react"
import { CSSProperties } from "react";
import { useSpring, useTransition, animated, easings, useSpringValue } from '@react-spring/web';

const AdvancedLoading = ({ size = 100 }) => {

  const faceStyle: CSSProperties = {
    position: 'absolute',
    width: size,
    height: size,
    border: '1px solid var(--border-2)',
    opacity: 0.2,
    backgroundColor: 'var(--primary-color)',
  }

  const faceZ = useSpringValue(size / 2);

  const springStyle = useSpring({
    from: {
      transform: 'rotateX(32deg) rotateY(45deg) rotateZ(0deg)',
    },
    to: async (next) => {
      while(true) {
        await next({
          transform: 'rotateX(32deg) rotateY(-225deg) rotateZ(0deg)',
        })
        await next({
          transform: 'rotateX(32deg) rotateY(45deg) rotateZ(0deg)',
        })
        await faceZ.start({
          to: size,
        });

        await faceZ.start({
          to: size / 2,
        });
      }
    },
    config: {
      tension: 130,
      friction: 30,
      mass: 1,

    }
  })

  return (
    <animated.div
      style={{
        pointerEvents: 'none',
        // contain: 'content',
        width: '100px',
        height: '100px',
        position: 'relative',
        transformStyle: 'preserve-3d',
        // transform: 'rotateX(32deg) rotateY(45deg)',
        // overflow: 'visible',
        // perspective: '800px'
        ...springStyle,
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center'
      }}
    >
      <animated.div 
        style={{
          ...faceStyle,
          rotateY: 0,
          z: faceZ,
        }}
      />
      <animated.div 
        style={{
          ...faceStyle,
          rotateY: 90,
          translateZ: faceZ
        }}
      />
      <animated.div 
        style={{
          ...faceStyle,
          rotateY: -90,
          translateZ: faceZ,
        }}
      />
      <animated.div 
        style={{
          ...faceStyle,
          rotateX: 90,
          translateZ: faceZ,
        }}
      />
      <animated.div 
        style={{
          ...faceStyle,
          rotateX: -90,
          translateZ: faceZ,
        }}
      />
      <animated.div 
        style={{
          ...faceStyle,
          rotateX: 180,
          translateZ: faceZ,
        }}
      />
    </animated.div>
  )
}

export default AdvancedLoading;