import { create } from 'zustand';
import evtManager from '../event';
import { EventType } from '../constant';

const useStatesStore = create<any>(set => ({
  clearStates: () => set(state => {
    const newState = state;
    for (const key in newState) {
      if (typeof newState[key] === 'function') {
        continue;
      }
      delete newState[key];
    }
    return { ...newState };
  }),
  initialStates: states => set({ ...states }),
  setState: (id, source, merge = true) => set(state => {
    const target = state[id];
    evtManager.emit(EventType.STATE_CHANGE, { id, newState: source, merge });
    if (merge && target) {
      return {
        [id]: { ...target, ...source },
      }
    }
    return { [id]: source };
  }),
}))

Object.defineProperty(window, '__STATES__', {
  get() {
    return useStatesStore.getState();
  },
})

export {
  useStatesStore,
}