import { useEffect } from "react";
import { CanvasPlugin } from "../../type";
import evtManager from "../../event";
import { EventType, GRID_ROW_HEIGHT } from "../../constant";
import { useSchemaStore } from "../../schema";
import { throttle } from 'lodash-es';
import useCanvasStore from "../store";
import { useShallow } from "zustand/react/shallow";
import { getCanvasRootElement, getComponentSizeInCanvas, getDropArea, getGridItemRowHeight, runAsync } from "../../misc";
import { getChildren } from "../../schema/utils";

class AdaptivePlugin implements CanvasPlugin {

  id: string = 'adaptive';

  heightIncreaseStep = GRID_ROW_HEIGHT;

  render() {
    const updateSchemaNode = useSchemaStore(state => state.updateNode);
    const previewLayout = useCanvasStore(useShallow(state => state.previewLayout));
    const resizeItem = useCanvasStore(state => state.resizeItem);
    const resizeLayout = useSchemaStore(useShallow(state => state.plainSchema[resizeItem?.id]?.layout));
    const plainSchema = useSchemaStore(state => state.plainSchema);
    const applyChanges = useSchemaStore(state => state.applyChanges);
    const previewScope = useCanvasStore(state => state.previewScope)

    useEffect(() => {
      // console.log('adaptive', previewScope);
      //  event.ts previewScope  previewLayout 
      if (!previewScope) {
        return;
      }
      const wipDropArea = getDropArea(previewScope);
      const gridItemHeihgt = getGridItemRowHeight(previewScope);
      const currentRowCount = Math.round(wipDropArea!.offsetHeight / gridItemHeihgt);
      let bottomRowNo = previewLayout.y + previewLayout.height;

      getChildren(previewScope).forEach(node => {
        const bottom = node.layout?.y + node.layout?.height;
        if (bottom) {
          bottomRowNo = Math.max(bottom, bottomRowNo);
        }
      });

      if (bottomRowNo > currentRowCount) {
        const nextHeight = bottomRowNo * gridItemHeihgt;
        applyChanges([{
          target: previewScope,
          type: 'update',
          payload: {
            style: {
              ...(plainSchema[previewScope].style || {}),
              height: nextHeight,
            }
          }
        }]);
      }
        // const componentSize = getComponentSizeInCanvas(previewScope);
        // console.log('componentWrapperSize', componentSize);
        // if (bottomRowNo <= componentSize.height / gridItemHeihgt && plainSchema[previewScope].style?.height > componentSize.height) {
        //   applyChanges([{
        //     target: previewScope,
        //     type: 'update',
        //     payload: {
        //       style: {
        //         ...(plainSchema[previewScope].style || {}),
        //         height: componentSize.height,
        //       }
        //     }
        //   }]);
        // }

    }, [previewLayout, resizeLayout?.height, resizeLayout?.y, plainSchema, previewScope]);

    return null;
  }
};

export default AdaptivePlugin;