import { create } from 'zustand';
import evtManager from '../event';
import { EventType } from '../constant';
import { useSchemaStore } from '../schema';

const useCanvasStore = create<any>(set => ({
  selected: '',
  hovered: '',
  inserting: '',
  isResizing: false,
  resizeItem: null,
  containers: {},
  // 
  previewLayout: {},
  previewScope: '',
  scrollTops: {},
  markResizeStart: (resizeItem) => set(({ resizeItem, isResizing: true })),
  markResizeEnd: () => set({ isResizing: false, resizeItem: null }),
  setSelected: id => set({ selected: id }),
  setHovered: id => set({ hovered: id }),
  setInserting: id => set({ inserting: id }),
  setContainer: (id, dom) => set(state => {
    const { containers } = state;
    containers[id] = dom;
    return { containers };
  }),
  deleteContainer: (id) => set(state => {
    const { containers } = state;
    delete containers[id];
    return { containers };
  }),
  setPreviewLayout: (previewLayout) => set({ previewLayout }),
}));

useCanvasStore.subscribe((state, prevState) => {
  // resize
  if (state.isResizing !== prevState.isResizing) {
    if (state.isResizing === true) {
      evtManager.emit(EventType.RESIZE_START, { item: state.resizeItem });
    } else {
      const layout = useSchemaStore.getState().plainSchema[prevState.resizeItem.id].layout;
      evtManager.emit(EventType.RESIZE_END, { item: prevState.resizeItem, prevLayout: prevState.resizeItem.layout, layout });
    }
  }
  // previewScope
  if (state.previewScope !== prevState.previewScope) {
    useSchemaStore.setState({ previewLayout: {} });
  }
});

export default useCanvasStore;