import { cloneDeep, every, forEach, isEqual, map } from "lodash-es";
import useDragStore from "../drag/dragStore";
import { getCanvasRootElement, hasCoincidence } from "../misc";
import { useSchemaStore } from "../schema";
import useCanvasStore from "./store";
import { GRID_COLUMN_COUNT, GRID_ROW_HEIGHT } from "../constant";
import { Component, ErrorInfo } from "react";


/**
 * resize
 */
export const shouldRestrict = (nextLayout) => {
  return false;
  const { isDragging, dragItem } = useDragStore.getState();
  const { isResizing, resizeItem } = useCanvasStore.getState();
  if (!isDragging && !isResizing) {
    return false;
  }
  const { schema } = useSchemaStore.getState();
  const firstClassChildren = schema.children || [];
  for (const child of firstClassChildren) {
    if (child.id === dragItem?.id || child.id === resizeItem?.id) {
      continue;
    }
    if (hasCoincidence(nextLayout, child.layout)) {
      return true;
    }
  }
  return false;
}

/**
 * core
 * @param id 
 * @param nextLayout
 * @param scope 
 * @returns newLayout
 */
export const collapseLayout = (nextLayout, lastLayout, scope = '') => {
  const { plainSchema, schema} = useSchemaStore.getState();
  const scopedSchema = plainSchema[scope] || schema;
  const topLayerNodes = scopedSchema.children;
  let newNextLayout = { ...nextLayout };
  const { isDragging, dragItem } = useDragStore.getState();
  const { isResizing, resizeItem } = useCanvasStore.getState();
  const id = dragItem?.id || resizeItem?.id;
  // 
  const changes: any[] = [];

  forEach(topLayerNodes, node => {
    if (node.id === id) {
      return;
    }
    if (!hasCoincidence(newNextLayout, node.layout)) {
      return;
    }
    const isHorizontalBetween = newNextLayout.x >= node.layout.x && newNextLayout.x + newNextLayout.width <= node.layout.x + node.layout.width;
    // 
    if (nextLayout.y >= node.layout.y && !isHorizontalBetween) {
      if (newNextLayout.x < node.layout.x) {
        // 
        newNextLayout.width = Math.max(node.layout.x - newNextLayout.x, 1);
      } else {
        // 
        const originalX = newNextLayout.x;
        newNextLayout.x = Math.max(node.layout.x + node.layout.width, 1);  
        newNextLayout.width = Math.max(newNextLayout.width - (newNextLayout.x - originalX), 1);
      }
    } else {
      if (isHorizontalBetween && newNextLayout.y > node.layout.y) {
        newNextLayout = { ...lastLayout };
      }
      if (newNextLayout.y >= node.layout.y + node.layout.height) {
        return;
      }
      // 
      const change = {
        target: node.id,
        type: 'update',
        payload: {
          layout: {
            ...node.layout,
            y: newNextLayout.y + newNextLayout.height,
          }
        }
      };
      changes.push(change); 
    }
  });
  // 
  let derivedChanges = [...changes];
  let wipDerivedChanges: any[] = [...derivedChanges];
  let loopCount = 0;
  /**
   * change
   * changechange
   *  lodash.isEqual 
   */
  do {
    if (loopCount > 10) {
      break;
    }
    // changecloneDeepderivedChangesderivedChangeswipDerivedChanges
    derivedChanges = cloneDeep(wipDerivedChanges)
    for (let index = 0; index < wipDerivedChanges.length; index++) {
      const change = wipDerivedChanges[index];
      const { layout } = change.payload;
      const changeCurrentLayout = plainSchema[change.target].layout;
  
      topLayerNodes.forEach(node => {
        if (node.id === change.target || node.id === id) {
          return;
        }
        if (!hasCoincidence(layout, node.layout)) {
          return;
        }
        const bottom = layout.y + layout.height;
        if (node.layout.y < bottom) {
          const existChange = wipDerivedChanges.find(({ target }) => target === node.id);
          if (existChange) {
            existChange.payload.layout = {
              ...existChange.payload.layout,
              y: bottom,
            }
            return;
          }
          wipDerivedChanges.push({
            target: node.id,
            type: 'update',
            derived: true,
            payload: {
              layout: {
                ...node.layout,
                y: bottom,
              }
            }
          })
        }
      });

      //  changeschange
      wipDerivedChanges.forEach(_change => {
        if (_change.target === change.target) {
          return;
        }
        if (!hasCoincidence(_change.payload.layout, change.payload.layout)) {
          return;
        }
        const _changeCurrentLayout = plainSchema[_change.target].layout;
        // change_change
        if (changeCurrentLayout.y < _changeCurrentLayout.y) {
          _change.payload.layout.y = change.payload.layout.y + change.payload.layout.height;
        } else {
          change.payload.layout.y = _change.payload.layout.y + _change.payload.layout.height;
        }
      });
      
    };
    loopCount++;
    // console.log(derivedChanges, wipDerivedChanges, isEqual(derivedChanges, wipDerivedChanges));
  } while (!isEqual(derivedChanges, wipDerivedChanges))

    // console.log('loopCount', loopCount);

  // console.log('derived times', loopCount);


  return [newNextLayout, derivedChanges];;
}

export class ErrorBoundary extends Component {

  componentDidCatch(error: Error, errorInfo: ErrorInfo): void {
    console.error(error)
  }


}