import MUIPaper from '@mui/material/Paper';
import React from 'react';
import PaperCover from '../thumbnails/Paper.jpg';

const Paper = props => {
  const { elevation, className, children, square, variant } = props;

  return (
    <MUIPaper 
      style={{ height:'100%' }} 
      elevation={elevation ?? undefined}
      className={className}
      square={square}
      variant={variant}
    >
      {children}
    </MUIPaper>
  )
}

Object.defineProperty(Paper, 'schema', {
  get() {
    return {
      name: '',
      type: 'paper',
      attributes: [
        {
          key: 'elevation',
          widget: 'select',
          name: 'elevation',
          type: 'property',
          defaultValue: 1,
          widgetProps: {
            options: [
              {
                label: 1,
                value: 1,
              },
              {
                label: 2,
                value: 2,
              },
              {
                label: 3,
                value: 3,
              },
            ]
          }
        },
        {
          key: 'square',
          widget: 'switch',
          name: '',
          type: 'property',
          defaultValue: false,
        },
        {
          key: 'variant',
          widget: 'select',
          name: '',
          type: 'property',
          defaultValue: 'elevation',
          widgetProps: {
            options: [
              {
                label: '',
                value: 'elevation',
              },
              {
                label: '',
                value: 'outlined',
              },
            ]
          },
        },
        {
          key: 'className',
          widget: 'input',
          name: 'css',
          type: 'property',
        },
      ],
      layout: {
        width: 2,
        height: 15,
        aspectRatio: 1
      },
      cover: PaperCover,
      hasSlot: true,
    }
  },
});

export default Paper;