import React from 'react';
import styles from './index.css';
import Canvas from './canvas';
import { useSchemaStore } from './schema';
import { useEffect } from 'react';
import { WindowMessageType } from './constant';
import useGlobalStore from './globalStore';
import { onGithubIo } from './misc';

const Previewer = () => {

  const initialSchema = useSchemaStore(state => state.initialSchema);
  const setMode = useGlobalStore(state => state.setMode);

  useEffect(() => {
    //  github.io  spa 
    if (onGithubIo()) {
      const schemaDataStr = localStorage.getItem('schemaData');
      if (schemaDataStr) {
        const schemaData = JSON.parse(schemaDataStr);
        console.log('session', schemaData);
        initialSchema(schemaData);
        setMode('preview');
        localStorage.removeItem('schemaData');
      }
      return;
    }
    const onMessage = e => {
      const { data: { data, type } } = e;
      if (type !== WindowMessageType.CANVAS_SCHEMA) {
        return;
      }
      console.log('receive', data);
      initialSchema(data);
      setMode('preview');
    }
    window.addEventListener('message', onMessage);
    return () => {
      window.removeEventListener('message', onMessage);
    }
  }, []);

  return (
    <div className={styles['previewer-canvas-container']}>
      <Canvas mode='preview' />
    </div>
  )
}

export default Previewer;