export const withManualFresh = Component => {
  return function (props) {
    const { schemaNode = {} } = props;
    const freshCtr = useSchemaStore(state => state.plainSchema[schemaNode.id].freshCtr);
    return <Component {...props} key={freshCtr}  />
  }
}

export const eventSealWrapper = Component => {
  return function(props) {

    const onKeyDown: ReactEventHandler = useCallback(e => {
      e.stopPropagation();
    }, []);
    return (
      <div
        onKeyDown={onKeyDown}
      >
        <Component {...props} />
      </div>
    )
  }
}

export const interLayerWrapper = Component => {
  return function(props) {
    const { schemaNode = {} } = props;
    const onScroll: ReactEventHandler = useCallback((e) => {
      //  tipLayer 
      pluginCenter.publishMessage(MessageBusChannel.CANVAS_PLUGIN_UPDATE_UI, {
        source: 'component.interLayerWrapper',
        timestamp: performance.now(),
        type: 'flushUI',
      });
      useCanvasStore.setState({ scrollTops: { ...useCanvasStore.getState().scrollTops, [schemaNode.id]: e.currentTarget.scrollTop } })
    }, []);
    return (
      <div className={styles['component-wrapper']}>
          <div 
            className={styles['component-wrapper-innner']}
            id={`scroll-wrapper-${schemaNode.id}`}
            onScroll={onScroll}
            style={{
              overflow: 'auto',
              // contain: 'strict',
              willChange: 'auto',
            }}
          >
            <Component {...props} />
          </div>
        </div>
    )
  }
}

export const dropWrapper = Component => {
  return function (props) {
    const { schemaNode = {} } = props;
    const schema = useMemo(() => {
      return getSchemaByType(schemaNode.type);
    }, [schemaNode.type])
    const { id } = schemaNode;
    const setInserting = useCanvasStore(state => state.setInserting);
    const isDragging = useDragStore(state => state.isDragging);
    const inserting = useCanvasStore(state => state.inserting);

    const { isDragOver } = useSchemaNode(id);

    const [, drop] = useDrop(() => ({
      accept: DragItemType.COMPONENT,
      hover(item: any, monitor) {
        console.log('dropWrapper hover', item);
      },
      canDrop(item, monitor) {
        return inserting === id;
      },
      drop(item: any, monitor) {
        const { type, name, source, id } = item;
        const { hasSlot } = schema;
        if (!hasSlot) {
          return;
        }
        if (source === DragSourceType.COMPONENT_PANEL) {
          // 
          const initialState = generateInitialState(item.schema);
          const newSchemaNode = {
            id: generateComponentId(type),
            type: type,
            state: initialState,
            layout: {
              type: LayoutType.GRID,
              width: item.schema.layout.width,
              height: item.schema.layout.height,
              inSlot: SlotType.COMMON,
            }
          };

          const { setState } = useStatesStore.getState();
          const { appendChild } = useSchemaStore.getState();

          setState(newSchemaNode.id, initialState);
          appendChild(schemaNode.id, newSchemaNode);
          mountMethods(newSchemaNode.id);
        }
        if (source === DragSourceType.CANVAS || source === DragSourceType.SLOT) {
          // 
          const { moveChild } = useSchemaStore.getState();
          moveChild(id, schemaNode.id, SlotType.COMMON);
        }
        evtManager.emit(EventType.DROP, { item, target: DragTargetType.SLOT });
      },
    }), [schema, schemaNode.id, setInserting, isDragging, inserting]);

    useEffect(() => {
      if (!schema.hasSlot || !isDragging) {
        return;
      }
      if (isDragOver) {
        setInserting(schemaNode.id);
      } else {
        if (inserting === schemaNode.id) {
          setInserting('');
        }
      }
    }, [isDragOver, schemaNode.id, schema, inserting, isDragging]);

    return (
      <div ref={drop} className={styles['component-drop-anchor']} >
        <Component {...props} />
      </div>
    )
  }
}

export const domRecordWrapper = (Component)  => {
  return (props) => {
    const { schemaNode = {} } = props;
    const { id } = schemaNode;

    const containerRef = useRef<HTMLDivElement>(null);
    const setContainer = useCanvasStore(state => state.setContainer);
    const deleteContainer = useCanvasStore(state => state.deleteContainer);
    useEffect(() => {
      if (!id || !containerRef.current) {
        return;
      }
      setContainer(id, containerRef.current);
      return () => {
        if (!id) {
          return;
        }
        deleteContainer(id);
      }

    }, [id, setContainer, deleteContainer]);

    return (
      <div id={id} className={styles['component-container-inner']} ref={containerRef}>
        <Component {...props} />
      </div>
    )
  }
}

export function dragWrapper(Component) {
  return (props) => {
    const { schemaNode = {}, _isRoot } = props;
    const { id, layout = {}, style = {}, name, type } = schemaNode;
    const componentInfo = useComponentStore(state => state[type]);
    const { schema } = componentInfo;
    const setSelected = useCanvasStore(state => state.setSelected);
    const setHovered = useCanvasStore(state => state.setHovered);
    const mode = useGlobalStore(state => state.mode);
    const isDraggingOnCanvas = useDragStore(state => state.isDragging);

    const onSelected = useCallback((e: React.MouseEvent) => {
      setSelected(id);
      e.stopPropagation();
    }, [setSelected]);

    const onMouseOver = useCallback((e: React.MouseEvent) => {
      setHovered(id);
      e.stopPropagation()
    }, [setHovered]);

    const onMouseOut = useCallback((e: React.MouseEvent) => {
      setHovered('');
      e.stopPropagation()
    }, []);

    const markDragging = useDragStore(state => state.markDragging);
    const markDragEnd = useDragStore(state => state.markDragEnd);

    const [, drag, preview] = useDrag(() => ({
      type: DragItemType.COMPONENT,
      collect: (monitor) => ({
        isDragging: monitor.isDragging(),
        cursorOffset: monitor.getInitialClientOffset(),
      }),
      canDrag(monitor) {
        return true;
      },
      item: (monitor: DragSourceMonitor) => {
        const dragItem = {
          type,
          name,
          itemType: DragItemType.COMPONENT,
          layout: { ...layout },
          source: DragSourceType.CANVAS,
          schema,
          id,
        };
        markDragging({ ...dragItem });
        return dragItem;
      },
      end(draggedItem, monitor) {
        markDragEnd()
      },
    }), [schema, markDragEnd, markDragging, id, layout]);

    useEffect(() => {
      const emptyImage = getEmptyImage();
      emptyImage.style.cursor = 'grabbing';
      preview(emptyImage, { captureDraggingState: true });
    }, [isDraggingOnCanvas]);

    return (
        <div
          ref={mode === 'edit' && !_isRoot ? drag : undefined}
          onClick={onSelected}
          onMouseOver={onMouseOver}
          onMouseOut={onMouseOut}
          className={styles['component-drag-anchor']}
          style={{ cursor: isDraggingOnCanvas ? 'grabbing' : '' }}
        >
          <Component {...props} style={style} schema={schema} />
        </div>
    )
  }
}

export function eventsWrapper(Component) {
  return (props) => {
    const id = props.schemaNode?.id;
    const events = useActionStore(state => state.events[id]);
    const setState = useStatesStore(state => state.setState);

    const eventMap = useShallowMemo(() => {
      const schema = getSchemaById(id);
      const groupedEvents = groupBy(events, 'type');
      schema.events?.forEach(event => {
        if (!groupedEvents[event.type]) {
          groupedEvents[event.type] = [];
        }
      });
      return mapValues(groupedEvents, (evts, eventType) => {
        return (e) => {
          // 
          const evtSchema = schema.events?.find(e => e.type === eventType);
          if (evtSchema?.bindState) {
            const newValue = getEventValue(e);
            setState(id, { [evtSchema.bindState]: newValue });
          }
          evts.forEach(evt => {
            try {
              evt.action.callback(e);
            } catch (error) {
              console.error('event exec error', evt, error);
            }
          });
        }
      });
    }, [id, events]);


    return <Component {...props} {...eventMap} />
  }
}

export function contextMenuWrapper(Component) {
  return (props) => {
    const id = props?.schemaNode?.id;
    const { _isRoot } = props;
    const setSelected = useCanvasStore(state => state.setSelected);
    const deleteNode = useSchemaStore(state => {
      return state.deleteNode
    });

    const menuItems = useMemo(() => {

      const items: any[] = [
        {
          key: 'delete',
          label: <Typography color="error" fontSize={12}></Typography>,
          hidden: _isRoot,
          onClick() {
            deleteNode(id);
          },
        }
      ];

      if (props.schemaNode.type === 'container') {
        items.unshift({
          key: 'heightAdaption',
          label: <Typography color="textPrimary" fontSize={12}></Typography>,
          onClick() {
            if (!id) {
              return;
            }
            const componentSize = getComponentSizeInCanvas(id);
            const bottomNode = getBottomNode(id);
            const bottomPx = bottomNode ? (bottomNode.layout.y + bottomNode.layout.height - 1) * getGridItemRowHeight(id) : 0;
            useSchemaStore.getState().applyChanges([{
              target: id,
              type: 'update',
              callback: () => {
                if (!bottomNode) {
                  return;
                }
                scrollIntoView(bottomNode.id, { behavior: 'smooth', block: 'end', inline: 'nearest', })
              },
              payload: {
                style: {
                  ...(props.schemaNode?.style || {}),
                  height: Math.max(componentSize.height, bottomPx),
                }
              }
            }]);
          },
        });
      }

      return items;
    }, [props.schemaNode?.type, _isRoot]);

    return (
      <ContextMenu
        menuItems={menuItems}
        onDisplay={() => {
          id && setSelected(id);
        }}
        style={{
          flex: 1,
          minHeight: 0,
        }}
      >
        <Component {...props} />
      </ContextMenu>
    )
  }
}

export function gridLayerWrapper(Component) {
  return (props) => {
    const { schemaNode = {} } = props;
    const layout = useSchemaStore(useShallow(state => ({
      x: state.plainSchema[schemaNode.id]?.layout.x,
      y: state.plainSchema[schemaNode.id]?.layout.y,
      width: state.plainSchema[schemaNode.id]?.layout.width,
      height: state.plainSchema[schemaNode.id]?.layout.height,
    }))) || {};
    const gridStyles: CSSProperties = {
      gridColumn: `${layout.x} / span ${layout.width || 1}`,
      gridRow: `${layout.y} / span ${layout.height || 1}`,
    };
    return <div
      className={styles['component-container']}
      style={{ ...gridStyles }}
    >
      <Component {...props} />
    </div>
  }
}


export function statesWrapper(Component) {
  return props => {
    const id = props.schemaNode?.id;
    const states = useStatesStore(states => states[id]);

    const [_, forceUpdate] = useReducer(x => x + 1, 0);
    if (id) {
      setForceUpdate(id, forceUpdate);
    }

    const isRoot = useSchemaStore(state => state.schema.id === props.schemaNode?.id);

    return (
      <Component {...props} {...states} _isRoot={isRoot} />
    )
  }
}