import { create } from 'zustand';
import { Action } from '../type';
import { processAction } from './utils';
import { generateShortId, isAfter, now } from '../misc';
import { pick } from 'lodash-es';
import { useSchemaStore } from '../schema';

export const useActionStore = create<any>((set, get) => ({
  events: {},
  addEvent: (id, event, writeIntoSchema = true) => set(state => {
    const { type, action } = event;
    const { events } = state;
    let componentEvents = events[id] || [];

    const eventId = generateShortId();
    const formatEvent = {
      id: eventId,
      mountId: id,
      type,
      action: processAction({ id, eventId, type, action }),
      createdAt: now(),
    }
    componentEvents.push(formatEvent);

    if (writeIntoSchema) {
      //  schema
      const { mountEvent } = useSchemaStore.getState();
      mountEvent(id, formatEvent);
    }

    events[id] = [...componentEvents];
    return {
      events,
    };
  }),
  updateEvent: (id, eventId, newEvent) => set(state => {
    const { events } = state;
    const componentEvents = events[id];

    if (!componentEvents) {
      return state;
    }

    const eventIdx = componentEvents.findIndex(event => event.id === eventId);
    if (eventIdx === -1) {
      return state;
    }

    const oldEvent = componentEvents[eventIdx];

    const processedNewEvent = {
      id: eventId,
      type: newEvent.type,
      action: processAction({ id, eventId, action: newEvent.action, type: newEvent.type }),
      ...pick(oldEvent, 'createdAt', 'mountId'),
      updatedAt: now(),
    }
    componentEvents.splice(eventIdx, 1, processedNewEvent);

    //  schema
    const { mountEvent } = useSchemaStore.getState();
    mountEvent(id, processedNewEvent);

    events[id] = [...componentEvents];

    return {
      events,
    }
  }),
  deleteEvent: (id, eventId) => set(state => {
    const { events } = state;
    const componentEvents = events[id];
    if (!componentEvents) {
      return state;
    }
    const evendIdx = componentEvents.findIndex(evt => evt.id === eventId);
    if (evendIdx === -1) {
      return state;
    }
    componentEvents.splice(evendIdx, 1);

    events[id] = [...componentEvents];

    //  schema
    const { removeEvent } = useSchemaStore.getState();
    removeEvent(id, eventId);

    return {
      events,
    }
  }),
  getLatestEvent: (id, includeUpdate?: boolean) => {
    const state = get();
    const componentEvents = state.events[id];
    if (!componentEvents?.length) {
      return null;
    }
    let workInProgress = [...componentEvents];
    workInProgress = workInProgress.sort((a, b) => {
      let dateA = a.createdAt;
      let dateB = b.createdAt;

      if (includeUpdate) {
        if (a.updatedAt) {
          dateA = a.updatedAt;
        }
        if (b.updatedAt) {
          dateB = b.updatedAt;
        }
      }
      
      return isAfter(dateA, dateB) ? -1 : 1;
    });

    return workInProgress[0];
  }
}));

Object.defineProperty(window, '__EVENTS__', {
  get() {
    return useActionStore.getState().events;
  },
})