export const generateComponentId = (type: string) => {
  const plainSchema = useSchemaStore.getState().plainSchema;
  const allNodes = Object.values(plainSchema);
  const specNodes = allNodes.filter((node: any) => node.type === type);
  let suffixIds: any[] = [];
  const suffixRegExp = new RegExp(`^${type}(\\d+)$`)
  specNodes.forEach((node: any) => {
    const matches = suffixRegExp.exec(node.id);
    if (!matches) {
      return;
    }
    suffixIds.push(Number(matches[1]));
  });

  if (!suffixIds.length) {
    return `${type}1`;
  }

  suffixIds = suffixIds.sort((a, b) => a - b);

  let i = 0;
  for (; i < suffixIds.length; i++) {
    if (i + 1 < suffixIds[i]) {
      break;
    }
  }
  return `${type}${i + 1}`;
}

export const generatePreviewInitialState =(schema: any = {}) => {
  const initialState = generateInitialState(schema);
  return mapValues(initialState, value => {
    if (typeof value === 'string' && expressionRegExp.test(value)) {
      return '';
    }
    return value;
  });
}

export const generateInitialState = (schema: any = {}) => {
  const { attributes } = schema;
  if (!Array.isArray(attributes)) {
    return {};
  }
  const initialState = {};
  attributes.forEach(attribute => {
    const { key, defaultValue } = attribute;
    initialState[key] = defaultValue;
  })
  return initialState;
}

export const getCanvasRootElement = () => {
  return document.getElementById('canvas-content-root');
}

export const getOffsetInRoot = ({ x, y }, root?: HTMLElement) => {
  const rootElement = root || getCanvasRootElement();
  if (!rootElement) {
    return { x, y };
  }
  const rootRect = rootElement.getBoundingClientRect();

  const relativeX = x - rootRect.x;
  const relativeY = y - rootRect.y;

  return { x: relativeX, y: relativeY };
}

