/**
 * mapper
 * @param obj 
 * @param mapper 
 * @returns 
 */
export function deepMap(obj, mapper, path: any[] = []) {
  const mapFn: any = Array.isArray(obj) ? map : mapValues;
  return mapFn(obj, (value, key) => {
    const newPath = [...path, key];
    if (isObject(value) && typeof value !== 'function') {
      return deepMap(value, mapper, newPath); 
    }
    return mapper(value, key, newPath);
  });
}

