export const isSystemInDarkMode = () => {
  return  window.matchMedia('(prefers-color-scheme: dark)').matches;
}

export const devExpose = (key: string, value) => {
  if (isDev()) {
    Object.defineProperty(window, key, {
      get() {
        return typeof value === 'function' ? value() : value;
      }
    })
  }
}

export const safeParseJSON = (input: any) => {
  try {
    return JSON.parse(input)
  } catch (error) {
    console.error('json parse error', error);
    return {};
  }
}

export const safeStringifyJSON = (input: any) => {
  try {
    return JSON.stringify(input)
  } catch (error) {
    console.error('json stringify error', error);
    return '{}';
  }
}

const generateRevertOperation = (operation: OperationLog): OperationLog => {
  const { type, details } = operation

  let revertOperation: Partial<OperationLog> = {
    operator: useGlobalStore.getState().userInfo.username,
    schema: useSchemaStore.getState().getSerializedSchema(),
    time: now(),
  };

  switch(type) {
    case OperationType.ADD_UI:
      revertOperation = {
        ...revertOperation,
        type: OperationType.REMOVE_UI,
        details: {
          revert: true,
          ...pick(details, 'id', 'itemType', 'name', 'type'),
        }
      }
      break;
    case OperationType.REMOVE_UI:
      revertOperation = {
        ...revertOperation,
        type: OperationType.ADD_UI,
        details: {
          revert: true,
          ...pick(details, 'id', 'itemType', 'name', 'type'),
        }
      }
      break;
    default:
      break;
  }

  return revertOperation as OperationLog;
}

export const undo = () => {
  const { operationStack, versionCursor } = useGlobalStore.getState();
  if (operationStack.length <= 1 || versionCursor <= 0) {
    return;
  }
  const operation = operationStack[versionCursor - 1];

  const { schema } = operation;
  const { initialSchema } = useSchemaStore.getState();
  initialSchema(safeParseJSON(schema));

  useGlobalStore.setState({ versionCursor: versionCursor - 1 });

  enqueueSnackbar('UNDO', {
    anchorOrigin: {
      vertical: 'bottom',
      horizontal: 'right',
    },
    variant: 'info',
    autoHideDuration: 3000,
    preventDuplicate: true,
  });

  // revert
  // runAsync(() => {
  //   const revertOperation = generateRevertOperation(operation);
  //   recordOperation(revertOperation);
  // });
}

export const redo = () => {
  const { operationStack, versionCursor } = useGlobalStore.getState();
  if (versionCursor >= operationStack.length - 1) {
    return;
  }
  const operation = operationStack[versionCursor + 1];

  const { schema } = operation;
  const { initialSchema } = useSchemaStore.getState();
  initialSchema(safeParseJSON(schema));

  useGlobalStore.setState({ versionCursor: versionCursor + 1 });

  enqueueSnackbar('REDO', {
    anchorOrigin: {
      vertical: 'bottom',
      horizontal: 'right',
    },
    variant: 'info',
    autoHideDuration: 3000,
    preventDuplicate: true,
  });
}

