export function getOS() {
  const platform = navigator.platform.toLowerCase();
  
  if (platform.includes('win')) {
    return 'Windows';
  }
  if (platform.includes('mac')) {
    return 'macOS';
  }
  if (platform.includes('linux')) {
    return 'Linux';
  }
  if (['iphone', 'ipad', 'ipod'].some(v => platform.includes(v))) {
    return 'iOS';
  }
  if (platform.includes('android')) {
    return 'Android';
  }
  
  return 'Unknown';
}

export const transformDataType = (data: any, accept: string) => {
  if (typeof data === accept) {
    return data;
  }
  let result = data;
  switch(accept) {
    case 'string':
      result = String(data);
      break;
    case 'number':
      result = Number(data);
      break;
    default:
      break;
  }

  return result;
}

