export const sleep = async (timeout: number) => {
  return new Promise<void>(resolve => {
    setTimeout(() => resolve(), timeout);
  })
}

export const transferPxStringToNumber = (px: string) => {
  return Number(px.split('px')[0])
}

/**
 * paddind
 * @param nodeId 
 */
export const getComponentSizeInCanvas = (nodeId) => {
  const scrollWrapper = getScrollWrapper(nodeId);
  const computedStyle = getComputedStyle(scrollWrapper);

  return {
    height: scrollWrapper.offsetHeight - transferPxStringToNumber(computedStyle.paddingTop) - transferPxStringToNumber(computedStyle.paddingBottom),
    width: scrollWrapper.offsetWidth -  transferPxStringToNumber(computedStyle.paddingLeft) - transferPxStringToNumber(computedStyle.paddingRight),
  }
}

export const getBottomNode = (scope: string) => {
  const scopedChildren = getChildren(scope);
  let bottomNode = scopedChildren?.[0] || null;
  scopedChildren.forEach(node => {
    const bottom = node.layout?.y + node.layout?.height;
    if (bottom > bottomNode.layout.y + bottomNode.layout.height) {
      bottomNode = node;
    }
  });
  return bottomNode
}

export const generateSafeContext = (whitelist: any) => {
  const context = Object.create(null);
  Object.assign(context, whitelist);

  return new Proxy(context, {
    // has(target, key) {
    //   return false;
    // },
    set() {
      return false;
    },
    get(target, key) {
      if (key in target) {
        return target[key];
      }
      if (typeof key !== 'symbol') {
        throw new Error(` ${key}`)
      }
    }
  })
}