:root[data-theme="light"]
{
  --primary-color: #1976d2;
  --bg-0: #fff;
  --canvas-gridline: #ccc;
  --container-bg-0: rgba(25, 118, 210, 0.87);
  --container-bg-1: #1976d2;
  --container-bg-2: #fff;
  --container-bg-3: #f6f6f6;
  --container-bg-4: #efefef;
  --container-bg-5: #efefef;
  --container-bg-hover-1: rgba(25, 118, 210, .20);
  --border-1: rgba(0, 0, 0, 0.23);
  --border-2: #e0e0e0;
  --border-focus-primary: #1976d2;
  --border-hover-1: rgba(0, 0, 0, 0.87);
  --cursor-1: #000000;
  --text-input-0: #ffffff;
  --text-input-1: #000000;
  --text-input-2: rgba(0, 0, 0, 0.85);
}

:root[data-theme="dark"]{
  --primary-color: #1976d2;
  --bg-0: #121212;
  --canvas-gridline: #555555;
  --container-bg-0: rgba(25, 118, 210, 0.87);
  --container-bg-1: #222222;
  --container-bg-2: #323232;
  --container-bg-3: #090909;
  --container-bg-4: #535353;
  --container-bg-5: #323232;
  --container-bg-hover-1: rgba(25, 118, 210, .20);
  --border-1: rgba(255, 255, 255, 0.23);
  --border-2: #101010;
  --border-focus-primary: #90caf9;
  --border-hover-1: #ffffff;
  --cursor-1: #ffffff;
  --text-input-0: #ffffff;
  --text-input-1: #ffffff;
  --text-input-2: rgba(255, 255, 255, 0.85);
}

.src-lowCode-index__low-code-container {
  display: flex;

  /* --text-1: #000;
  --text-2: #555555; */
}

.src-lowCode-index__canvas-container {
  height: calc(100vh - 48px);
  border: 1px solid var(--border-1);
  position: relative;
  overflow: auto;
  flex: 1;
  min-width: 400px;
  /* margin-top: 12px; */
  padding: 24px;
  background-color: var(--container-bg-4);
}

body {
  padding: 0;
  margin: 0;
}

.src-lowCode-index__previewer-canvas-container {
  height: calc(100vh - 48px);
}

.src-lowCode-index__left-side-tool-container {
  /* flex: 0; */
  /* overflow: auto; */
  flex: 0 0 235px;
}

