import { Box, LinearProgress, Typography } from "@mui/material"
import React from "react"
import AdvancedLoading from "./toys/advancedLoading"
import { Suspense } from "react"
import { lazy } from "react"
import { useMemo } from "react"
import { useState } from "react"
import { createPortal } from "react-dom"
import { useSprings, animated } from "@react-spring/web";
import { isDev, sleep } from "./misc"

const welcomeChars = 'Welcome'.split('');

const LazyEditor = () => {

  const [loaded, setLoaded] = useState(false);

  const Editor = useMemo(() => {
    const time1 = performance.now();
    return lazy(() => import('./editor').then(res => {
      const time2 = performance.now();
      const cost = time2 - time1;
      console.log('load editor cost', cost, 'ms');
      if (cost > 4000 || isDev()) {
        setLoaded(true);
        return res;
      }
      // mock
      return new Promise<any>(resolve => {
        setTimeout(() => {
          setLoaded(true);
          resolve(res);
        }, 4000 + Math.random() * 6000);
      })
    }));
  }, []);

  const [welcomeProps] = useSprings(7, (i, ctr) => ({
    delay: 150 * i,
    pause: true,
    from: {
      y: 0,
    },
    to: async(next) => {

      while(true) {
        await next({
          y: -2,
          config: {
            restVelocity: 20,
          }
        });
        await next({
          y: 0,
          config: {
            restVelocity: 20,
            // velocity: 0.05
          }
        });

        await sleep(2000);
      }
    },
    // loop: true,  
    config: {
      mass: 2,
      tension: 500,
      friction: 40,
      // clamp: true,

    },
  }), []);

  return (
    <div>
      {!loaded && (
        createPortal(
        <Box
          position={'fixed'}
          height={'100%'}
          width={'100%'}
          display={'flex'}
          alignItems={'center'}
          justifyContent={'center'}
          bgcolor={'var(--container-bg-2)'}
          zIndex={1209}
          flexDirection={'column'}
        >
          <AdvancedLoading />
          <LinearProgress
            sx={{
              width: '100%',
              position: 'fixed',
              top: 0,
            }}
          />
          <Box
            display={'flex'}
          >
            {welcomeProps.map((props, index) => (
              <animated.div
                key={index}
                style={props}
              >
                <Typography mt={10} fontSize={18} sx={{ fontWeight: '1000' }}>{welcomeChars[index]}</Typography>
              </animated.div>
            ))}
          </Box>
        </Box>, document.getElementById('root')!)
      )}
      <Suspense>
        <Editor />
      </Suspense>
    </div>
  )
}

export default LazyEditor;