import { useEffect } from "react";
import { CanvasPlugin } from "../../type";
import { redo, runAsync, undo } from "../../misc";
import { useRef } from "react";
import MessageBus from "../../utils/messageBus";
import { MessageBusChannel } from "../../constant";
import useCanvasStore from "../store";
import { useSchemaStore } from "../../schema";
import { enqueueSnackbar } from "notistack";

class ShortcutPlugin implements CanvasPlugin {

  id: string = 'shortcut';

  messageBus: MessageBus;

  beforeRegister?: ((messageBus: MessageBus) => void) | undefined = function (messageBus) {
    this.messageBus = messageBus;
  };

  hotKeyConfig = [
    {
      key: 'cmd-z',
      callback: (e) => undo(),
    },
    {
      key: 'cmd-shift-z',
      callback: (e) => redo(),
    },
    {
      key: 'cmd',
      callback: () => {
        this.messageBus.publish(MessageBusChannel.CANVAS_PLUGIN_UPDATE_UI, {
           type: 'updateVisibility', source: this.id, payload: { target: 'gridLayer', value: true }, timestamp: Date.now()
        });
        return () => {
          this.messageBus.publish(MessageBusChannel.CANVAS_PLUGIN_UPDATE_UI, {
            type: 'updateVisibility', source: this.id, payload: { target: 'gridLayer', value: false }, timestamp: Date.now()
         });
        }
      },
    },
    {
      key: 'Backspace',
      callback: () => {
        const { selected } = useCanvasStore.getState();
        const { plainSchema } = useSchemaStore.getState();
        if (!selected) {
          return;
        }
        useSchemaStore.getState().deleteNode(selected);
        runAsync(() => {
          if (!plainSchema[selected]) {
            enqueueSnackbar(` ${selected}`, {
              hideIconVariant: true,
              anchorOrigin: {
                vertical: 'bottom',
                horizontal: 'right',
              },
              variant: 'error'
            })
          }
        });
      },
    }, 
]

  render() {

    const destroies = useRef<any[]>([]);

    useEffect(() => {
      const handleKeyDown = (event: KeyboardEvent) => {
        this.hotKeyConfig.forEach(({ key, callback }) => {
          const combo = key.split('-').map(k => k.toLowerCase());
          const conditions = [
            combo.includes('ctrl') === event.ctrlKey,
            combo.includes('alt') === event.altKey,
            combo.includes('shift') === event.shiftKey,
            (combo.includes('meta') || combo.includes('cmd')) === event.metaKey,
            combo.some(_key => 
              !['ctrl', 'alt', 'shift', 'meta', 'cmd'].includes(_key) && 
              _key === event.key.toLowerCase()
            )
          ];
          
          if (key.toLowerCase() === 'cmd') {
            key = 'meta';
          }
          if (conditions.every(Boolean) || key.toLowerCase() === event.key.toLowerCase()) {
            event.preventDefault();
            const destroy = callback(event);
            if (typeof destroy === 'function') {
              destroies.current.push(destroy);
            }
          }
        })
      };

      const handleKeyUp = () => {
        let destroy: any;
        while(destroy = destroies.current.shift()) {
          destroy();
        }
      }
  
      document.addEventListener('keydown', handleKeyDown);
      document.addEventListener('keyup', handleKeyUp);
      return () => {
        document.removeEventListener('keydown', handleKeyDown);
        document.removeEventListener('keyup', handleKeyUp);
      }
    }, [this.hotKeyConfig]);

    return null;
  }
} 

export default ShortcutPlugin;