import Button from "./Button";
import Paper from "./Paper";
import Input from "./Input";
import { contextMenuWrapper, domRecordWrapper, dragWrapper, dropWrapper, eventSealWrapper, eventsWrapper, gridLayerWrapper, interLayerWrapper, statesWrapper, withManualFresh } from "./wrappers";
import Container from "./Container";


const wrapperList: any[] = [
  withManualFresh,
  interLayerWrapper,
  eventSealWrapper,
  // dropWrapper, 
  dragWrapper, 
  domRecordWrapper,
  eventsWrapper, 
  contextMenuWrapper,
  gridLayerWrapper, 
  statesWrapper,
];

const Components = {
  input: {
    Component: Input,
    RawComponent: Input,
    type: 'input',
    schema: (Input as any).schema,
  },
  button: {
    Component: Button,
    RawComponent: Button,
    type: 'button',
    schema: (Button as any).schema,
  },
  paper: {
    Component: Paper,
    RawComponent: Paper,
    type: 'paper',
    schema: (Paper as any).schema,
  },
  container: {
    Component: Container,
    RawComponent: Container,
    type: 'container',
    schema: (Container as any).schema,
  }
};

export function processComponentInfo(info) {
  wrapperList.forEach(wrapper => {
    info.Component = wrapper(info.Component);
  });
  return info;
}

function process() {
  Object.values(Components).forEach(processComponentInfo);
}

process();

export default Components;