import { CSSProperties } from "react";
import useCanvasRect from "./useCanvasLayout";
import { GRID_COLUMN_COUNT, GRID_ROW_HEIGHT } from "../constant";
import { useMemo } from "react";

const useGridStyle = (drawLines = true, dom?, gridConfig: any = {}) => {

  const canvasRect = useCanvasRect(dom);

  const gridColumnCount = gridConfig.columnCount || GRID_COLUMN_COUNT;
  const gridRowHeight = gridConfig.rowHeight || GRID_ROW_HEIGHT;

  const gridBgStyles: CSSProperties = useMemo(() => {
    const styles: CSSProperties = {
      backgroundSize: `${(canvasRect?.width || 0) / gridColumnCount}px ${gridRowHeight}px`,
      display: 'grid',
      gridTemplateRows: `repeat(auto-fill, ${gridRowHeight}px)`,
      gridTemplateColumns: `repeat(${gridColumnCount}, 1fr)`,
    };
    if (!drawLines) {
      delete styles.backgroundImage;
      delete styles.backgroundSize;
    }
    return styles;
  }, [canvasRect?.width, drawLines, gridColumnCount, gridRowHeight]) ;

  return gridBgStyles;
}

export default useGridStyle;

