import { useSchemaStore } from ".";

export function findParent(nodeId: string) {
  const node = useSchemaStore.getState().plainSchema[nodeId];
  return node?.parent;
}

export const getChildren = (nodeId: string) => {
  const node = useSchemaStore.getState().plainSchema[nodeId];
  return node?.children || [];
}

export function isRoot(nodeId: string) {
  return useSchemaStore.getState().schema.id === nodeId;
}