import { create } from 'zustand';
import Components from '.';
import { useSchemaStore } from '../schema';
import { pickBy } from 'lodash-es';

const useComponentStore = create<any>((set, get) => ({
  initial: Components => set({ ...Components }),
  updateComponent: (type, Component) => set({ [type]: Component }),
  clearDebug: () => set(state => {
    Object.keys(state).forEach(key => {
      const info = state[key];
      if (info.mark === 'debug') {
        delete state[key];
      }
    })
    return {};
  }),
  getComponentsInfo: () => {
    return pickBy(get(), (value) => typeof value !== 'function');
  }
}));

useComponentStore.getState().initial(Components);

useComponentStore.subscribe((state, prevState) => {
  const componentInfos = state.getComponentsInfo();
  const { plainSchema, deleteNode } = useSchemaStore.getState();
  const nodeToDelete = Object.values(plainSchema).filter((node: any) => !componentInfos[node.type]);
  nodeToDelete.forEach((node: any) => {
    deleteNode(node.id, false)
  });
})

export default useComponentStore;