import { create, StoreApi } from 'zustand';
import { subscribeWithSelector } from 'zustand/middleware';
import { devExpose, getDefaultOperationLog, isSystemInDarkMode } from './misc';
import { OperationLog } from './type';

const useGlobalStore = create<any>()(subscribeWithSelector<any>(set => ({
  mode: location.pathname.includes('/preview') ? 'priview' : 'edit',
  userInfo: { username: '', uid: 9527, jwt: '' },
  theme: localStorage.getItem('theme') || (isSystemInDarkMode?.() ? 'dark' : 'light'),
  operationStack: [],
  versionCursor: 0,
  isDebugging: false,
  setUser: userInfo => set({ userInfo }),
  setMode: mode => set({ mode }),
  setTheme: theme => set({ theme }),
  toggleTheme: () => set(state => {
    const { theme } = state;
    if (theme === 'light') {
      return {
        theme: 'dark',
      }
    }
    if (theme === 'dark') {
      return {
        theme: 'light',
      }
    }
  }),
  recordOperation: (operation) => set(state => {
    const { operationStack } = state;
    const newStack = [...operationStack, operation];
    if (newStack.length > 20) {
      newStack.splice(0, newStack.length - 20);
    }
    return {
      operationStack: newStack,
    }
  }),
})));

useGlobalStore.subscribe(state => state.operationStack, (stack) => {
  const { versionCursor } = useGlobalStore.getState();
  if (versionCursor !== stack.length - 1) {
    const newStack = [...stack];
    newStack.splice(versionCursor + 1, stack.length - versionCursor - 2);
    useGlobalStore.setState({ operationStack: newStack, versionCursor: newStack.length - 1 });
  }
});

export const recordOperation = (operation: OperationLog) => {
  const { versionCursor, operationStack } = useGlobalStore.getState();
  if (versionCursor === operationStack.length - 1) {
    useGlobalStore.setState({ versionCursor: versionCursor + 1 });
  }
  useGlobalStore.getState().recordOperation(operation);
}
// 
setTimeout(() => {
  devExpose('__GLOBAL__', () => useGlobalStore.getState());
  useGlobalStore.setState({ operationStack: [getDefaultOperationLog()] });
});

export default useGlobalStore;