import ee from 'event-emitter';
import { DragSourceType, EventType } from '../constant';
import useDragStore from '../drag/dragStore';
import useCanvasStore from '../canvas/store';
import { compileExpression, updateExpressionContext } from '../expression';
import { get, isEqual, mapValues, set, some } from 'lodash-es';
import { deps, useSchemaStore } from '../schema';
import { useStatesStore } from '../states';
import { getScrollWrapper, now, runAsync, safeStringifyJSON } from '../misc';
import { eventDeps } from '../action/utils';
import { useActionStore } from '../action';
import useGlobalStore, { recordOperation } from '../globalStore';
import { findParent } from '../schema/utils';

/**
 * 
 */
const evtManager = ee();

evtManager.on(EventType.DROP, (e) => {
  const { item } = e;
  const { markDragEnd } = useDragStore.getState();
  const { setInserting, setSelected } = useCanvasStore.getState();
  markDragEnd();
  setInserting('');
  if (item.id) {
    setSelected(item.id);
  }
  // add
  if (item.source === DragSourceType.COMPONENT_PANEL) {
    updateExpressionContext();
    runAsync(() => {
      recordOperation({
        type: 'addUI',
        name: '',
        operator: useGlobalStore.getState().userInfo.username,
        time: now(),
        schema: safeStringifyJSON(useSchemaStore.getState().getSerializedSchema()),
        details: {
          ...item,
          source: 'componentPanel',
          action: 'drag',
        },
        version: useGlobalStore.getState().versionCursor + 1,
      });
    })
  }

  if (item.source === DragSourceType.CANVAS) {
    if (!isEqual(e.layout, e.prevLayout)) {
      runAsync(() => {
        recordOperation({
          type: 'moveUI',
          name: '',
          operator: useGlobalStore.getState().userInfo.username,
          time: now(),
          schema: safeStringifyJSON(useSchemaStore.getState().getSerializedSchema()),
          details: {
            ...item,
            source: 'canvas',
            action: 'drag',
          },
          version: useGlobalStore.getState().versionCursor + 1,
        });
      })
    }
  }

  if (item.source === DragSourceType.SLOT) {
    runAsync(() => {
      recordOperation({
        type: 'moveUI',
        name: '',
        operator: useGlobalStore.getState().userInfo.username,
        time: now(),
        schema: safeStringifyJSON(useSchemaStore.getState().getSerializedSchema()),
        details: {
          ...item,
          source: 'canvas',
          action: 'drag',
        },
        version: useGlobalStore.getState().versionCursor + 1,
      });
    })
  }

  const parentNode = findParent(item.id);
  const scrollTop = useCanvasStore.getState().scrollTops[parentNode.id];
  if (scrollTop >= 0) {
    const scrollWrapper = getScrollWrapper(parentNode.id);
    runAsync(() => {
      scrollWrapper.scrollTop = scrollTop;
    })
  }
  //  preview  adaptive 
  runAsync(() => {
    useCanvasStore.setState({ previewLayout: {}, previewScope: '' });
  })

});

evtManager.on(EventType.STATE_CHANGE, ({ id, newState, merge }) => {
  //  zustand 
  runAsync(() => {
    // 
    mapValues(deps, (nodeDepList, path) => {
      const needUpdate = some(nodeDepList, (dep: any) => {
        return dep.id === id && dep.key in newState;
      });
      if (needUpdate) {
        const [nodeId, ...statePath] = path.split('.');
        const expression = get(useSchemaStore.getState().plainSchema[nodeId].state, statePath);
        const [expressionReturn] = compileExpression(expression);
        const updateState = set({}, statePath, expressionReturn);
        useStatesStore.getState().setState(nodeId, updateState);
      }
    });
    // 
    mapValues(eventDeps, (eventDepList, path) => {
      const needUpdate = some(eventDepList, (dep: any) => {
        return dep.id === id && dep.key in newState;
      });
      if (needUpdate) {
        const [mountId, eventId, ...optionsPath] = path.split('.');
        const { updateEvent, events } = useActionStore.getState();
        updateEvent(mountId, eventId, events[mountId].find(e => e.id === eventId));
      }
    });
  });
});

evtManager.on(EventType.RESIZE_START, e => {
  const { item } = e;
  const resizeScope = findParent(item.id)?.id;
  if (resizeScope) {
    useCanvasStore.setState({ previewScope: resizeScope });
  }
});

evtManager.on(EventType.RESIZE_END, (e) => {
  const { item, prevLayout, layout } = e;
  if (!isEqual(prevLayout, layout)) {
    runAsync(() => {
      recordOperation({
        type: 'resizeUI',
        name: '',
        operator: useGlobalStore.getState().userInfo.username,
        time: now(),
        schema: safeStringifyJSON(useSchemaStore.getState().getSerializedSchema()),
        details: {
          ...item,
        },
        version: useGlobalStore.getState().versionCursor + 1,
      });
    });
  }
  useCanvasStore.setState({ previewScope: '' });
})

evtManager.on(EventType.DELETE_NODE, e => {
  const { node, manual = true } = e;
  if (manual) {
    runAsync(() => {
      recordOperation({
        type: 'deleteNode',
        name: '',
        operator: useGlobalStore.getState().userInfo.username,
        time: now(),
        schema: safeStringifyJSON(useSchemaStore.getState().getSerializedSchema()),
        details: {
          ...node,
        },
        version: useGlobalStore.getState().versionCursor + 1,
      });
    });
  }
});

Object.defineProperties(window, {
  __EVT_MANAGER__: {
    get() {
      return evtManager;
    },
  }
});

export default evtManager;


