import { Message } from "../type";

export class MessageBus {
  private channels: Record<string, Function[]> = {};

  publish(channel: string, message: Message) {
    (this.channels[channel] || []).forEach(handler => handler(message));
  }

  subscribe(channel: string, handler: Function) {
    if (!this.channels[channel]) this.channels[channel] = [];
    this.channels[channel].push(handler);
    return () => this.unsubscribe(channel, handler);
  }

  unsubscribe(channel: string, handler: Function) {
    this.channels[channel] = (this.channels[channel] || [])
      .filter(h => h !== handler);
  }
}

export default MessageBus;