import React from "react";
import buttonCover from '../thumbnails/Button.jpg';
import { Button as MUIButton } from '@mui/material';
import { ControlComponentContext, FormItemType } from "../../type";

const Button = props => {
  const { text, click, variant, disabled, size, color, loading } = props;

  return (
    <MUIButton
      onClick={click}
      variant={variant}
      disabled={disabled}
      size={size}
      color={color}
      loading={loading}
      style={{
        width: '100%',
        height: '100%',
        padding: 0,
        minWidth: 0,
      }}
    >
      {text}
    </MUIButton>
  );
}

Object.defineProperty(Button, 'schema', {
  get() {
    return {
      name: '',
      type: 'button',
      attributes: [
        {
          key: 'text',
          widget: 'editor',
          name: '',
          accept: 'string',
          defaultValue: '',
          widgetProps: {
            tipProps: {
              titles: ['text', ''],
              description: '',
              example: '{{ button1.size }}'
            }
          },
        },
        {
          key: 'variant',
          widget: 'select',
          name: '',
          defaultValue: 'contained',
          widgetProps: {
            options: [
              {
                value: 'contained',
                label: '',
              },
              {
                value: 'outlined',
                label: '',
              },
              {
                value: 'text',
                label: '',
              }
            ]
          }
        },
        {
          key: 'size',
          widget: 'select',
          name: '',
          defaultValue: 'medium',
          widgetProps: {
            options: [
              {
                value: 'small',
                label: '',
              },
              {
                value: 'medium',
                label: '',
              },
              {
                value: 'large',
                label: '',
              }
            ]
          }
        },
        {
          key: 'color',
          widget: 'select',
          name: '',
          defaultValue: 'primary',
          widgetProps: {
            options: [
              {
                value: 'primary',
                label: '',
              },
              {
                value: 'secondary',
                label: '',
              },
              {
                value: 'success',
                label: '',
              },
              {
                value: 'info',
                label: '',
              },
              {
                value: 'warning',
                label: '',
              },
              {
                value: 'error',
                label: '',
              },
            ]
          }
        },
        {
          key: 'disabled',
          widget: 'switch',
          name: '',
          defaultValue: false,
        },
        {
          key: 'loading',
          widget: 'switch',
          name: '',
          defaultValue: false,
        },
      ],
      events: [
        {
          type: 'click',
          name: '',
        }
      ],
      methods: [
        {
          type: 'setContent',
          name: '',
          optionSchema: [
            {
              type: FormItemType.EDITOR,
              field: 'content',
              label: '',
            }
          ],
          handler: (context: ControlComponentContext) => {
            const { self, api, options } = context;
            api.setStates(self, { text: options.content });
          }
        }
      ],
      layout: {
        width: 1,
        height: 5,
        minHeight: 4,
        aspectRatio: 2,
      },
      cover: buttonCover,
    }
  },
});

export default Button;